import * as React from 'react';

interface Props {
  learningGoal?: string;
  onSave: (learningGoal: string) => void;
}

interface State {
  learningGoal: string;
}

export class AddCustomGoal extends React.Component<Props,State> {
  constructor (props: Props) {
    super(props)
    this.state = {
      learningGoal: props.learningGoal || ''
    }
  }

  render() {
    const {learningGoal} = this.state;
    const {onSave} = this.props;

    return <div>
      <div className="sv-html-portlet sv-portlet sv-skip-spacer">
          <h2>Lgg till eget lrandeml</h2>
          <hr />
          <div className="align-horizontal">
            <div className="horizontalItem top">Ml:</div>
            <div className="horizontalItem top">
              <input type="text" tabIndex={1} size={50} autoFocus
                value={learningGoal}
                onChange={e => this.setState({learningGoal: e.target.value})} />
            </div>
            <div className="horizontalItem top spaced" />
          </div>
          <br /></div>
      <div className="sv-html-portlet sv-portlet">
        <button
          tabIndex={1}
          className="btn btn-large btn-default"
          onClick={()=>onSave(learningGoal)}>Spara</button>
      </div>
    </div>
  }
}

