import * as React from 'react';
import { UserTasksBox } from './user-tasks-box';
import { UserTask, UserSubTask } from 'kedbackend/models';
import { createUUID, HttpError } from "kedbackend/client";
import { refine, VirtualLearningGoal } from './refiner';
import env from '../../globals/KED.env';
import { userTasksRepo } from '../../repos/user-tasks-repo';
import { L, compareProp } from '../../utils/utils';
import moment from 'moment';
//import { CustomGoal } from '../../contracts/ked-models';
import { TSON } from '../../utils/tson';
import { WeekPlannerPersistedState } from './weekplanner-persisted-state';
import { RemoveItem } from '../course-builder/sub-components/remove-item';
import { Dialogs } from '../utility-components/dialogs';
import { AddCustomGoal } from './add-custom-goal';
import { AddCustomTask } from './add-custom-task';
import { EditUserTask } from './edit-user-task';
import { Spinner } from '../course-builder/sub-components/spinner';
import { OpenCloseBox } from '../utility-components/open-close-box';
import { PendingJob } from '../../utils/pending-job';
import { AddOrEditSubTask } from './add-or-edit-sub-task';
import { getAdjustedWeek, KEDWeek } from '../../utils/weekutil';

interface Props {
  viewCourseUrl: string;
}

interface State {
  weekDate: number;
  dialogs: any[];
  userTasks: UserTask[];
  openCourses: { [path: string]: boolean }
  strategy?: string;
  assessment?: string;
  weekTextsUT: UserTask | null;
  saving: boolean;
  isLoading: boolean;
}

export class WeekPlanner extends React.Component<Props,State> {
  weekTextsSavingJob: PendingJob;

  constructor(props: Props) {
    super(props);
    this.state = {
      userTasks: [],
      weekDate: Date.now(),
      openCourses: {},
      dialogs: [],
      saving: false,
      weekTextsUT: null,
      isLoading: true
    }
    this.onChange = this.onChange.bind(this);
    this.weekTextsSavingJob = new PendingJob(()=>this.saveWeekTexts());
  }

  componentDidMount() {
    userTasksRepo.subscribe(this.onChange);
  }

  componentWillUnmount() {
    userTasksRepo.unsubscribe(this.onChange);
    this.weekTextsSavingJob.stop();
  }

  onChange(userTasks: UserTask[], persisted: WeekPlannerPersistedState, weekTextsUT: UserTask) {
    const newState : Partial<State> = {
      userTasks,
      weekDate: persisted.weekDate,
      openCourses: persisted.openCourses,
      weekTextsUT,
      isLoading: false
    }
    if (!this.state.weekTextsUT || (weekTextsUT.dateTime !== this.state.weekTextsUT.dateTime)) {
      // Changing week. Reset to new week's values
      newState.strategy = weekTextsUT.weekTexts.strategy;
      newState.assessment = weekTextsUT.weekTexts.assessment;
    }
    this.setState(newState as any); // React's use of Pick instead of Partial makes things complex here.
  }

  isWeekTextsEdited() {
    const { strategy, assessment, weekTextsUT } = this.state;
    return !!weekTextsUT && (strategy !== weekTextsUT.weekTexts.strategy || assessment !== weekTextsUT.weekTexts.assessment);
  }

  private async saveWeekTexts() {
    console.log("Saving texts...");
    const { strategy, assessment, weekTextsUT } = this.state;
    if (this.isWeekTextsEdited()) {
      await userTasksRepo.upsert(weekTextsUT, ut => {
        ut.weekTexts = { strategy, assessment };
      });
    }
  }

  prevWeek() {
    //this.saveWeekTexts();
    userTasksRepo.changeWeek(moment(this.state.weekDate).add(-1, 'week').toDate());
  }

  nextWeek() {
    //this.saveWeekTexts();
    userTasksRepo.changeWeek(moment(this.state.weekDate).add(1, 'week').toDate());
  }

  openDialog(dialog) {
    this.setState({ dialogs: [...this.state.dialogs, dialog] });
  }

  openAddGoalDialog() {
    this.openDialog(<AddCustomGoal
      onSave={async (learningGoal: string) => {
        if (!learningGoal) throw new Error (L`Lrandemlet kan inte vara tomt`);
        if (!this.state.saving) {
          this.setState({ saving: true });
          try {
            await this.addCustomGoal(learningGoal);
          } finally {
            this.setState({ saving: false });
          }
          this.closeDialog();
        }
      }} />);
  }

  openAddOwnTaskDialog(courseName: string, learningGoalName: string) {
    this.openDialog(<AddCustomTask
      isTask={!courseName}
      onSave={async (name: string, description: string, url: string) => {
        if (!name) throw new Error (L`Namnet kan inte vara tomt`);
        if (!this.state.saving) {
          this.setState({ saving: true });
          try {
            await this.addCustomTask(courseName, learningGoalName, name, description, url);
          } finally {
            this.setState({ saving: false });
          }
          this.closeDialog();
        }
      }} />);
  }

  editTask(userTask: UserTask) {
    this.openDialog(<EditUserTask
      userTask={userTask}
      onUpdate={async (updater: (ut: UserTask) => void) => {
        const test = {...userTask};
        updater(test);
        if (!test.name) throw new Error (L`Namnet fr inte vara tomt`);
        this.closeDialog();
        await userTasksRepo.update([userTask], updater);
      }}
      onDelete={async id => {
        this.closeDialog();
        await userTasksRepo.delete([id]);
      }}
      openDialog={dialog=>this.openDialog(dialog)}
      closeDialog={()=>this.closeDialog()}
    />);
  }

  editSubTask(userTask: UserTask, subTask: UserSubTask) {
    this.openDialog(<AddOrEditSubTask
      mode="edit"
      userTask={userTask}
      subTask={subTask}
      closeDialog={()=>this.closeDialog()}
    />);
  }

  closeDialog() {
    this.setState({ dialogs: this.state.dialogs.slice(0, this.state.dialogs.length - 1) });
  }



  async addCustomGoal(learningGoal: string) {
    let latestTimeStamp = Math.max.apply(
      Math.max,
      [this.state.weekDate].concat(this.state.userTasks.map(t => t.dateTime)));

    await userTasksRepo.insert([{
      id: createUUID(),
      learningGoal,
      dateTime: latestTimeStamp + 2000
    }]);

    await userTasksRepo.setWeekPlannerBoxOpen("", true);
  }

  async removeLearningGoal(learningGoal: VirtualLearningGoal) {
    if (learningGoal.tasks.length > 0) {
      if (!confirm(`Ta bort lrandeml samt ${learningGoal.tasks.length} uppgifter?`)) {
        return;
      }
    }
    await userTasksRepo.delete(learningGoal.allTasks.map(t => t.id));
  }

  async addCustomTask(courseName: string, learningGoal: string, name: string, description: string, url: string) {
    let latestTimeStamp = Math.max.apply(
      Math.max,
      [this.state.weekDate].concat(this.state.userTasks.map(t => t.dateTime)));

    /*const emptyLearningGoalPlaceholder = this.state.userTasks.find(ut =>
      ut.name == null &&
      !ut.courseName &&
      ut.learningGoal === learningGoal);

    if (emptyLearningGoalPlaceholder) {
      // Hijack learning-goal placeholder and make it the real task.
      // This will make the learning goal disappear once this task
      // is deleted.
      await userTasksRepo.update([emptyLearningGoalPlaceholder], ut => {
        Object.assign(ut, { name, description, url });
      });
    } else {*/
      // Add another task to same learning-goal
      await userTasksRepo.insert([{
        id: createUUID(),
        dateTime: latestTimeStamp + 2000,
        courseName,
        learningGoal,
        name,
        description,
        url
      }]);
    //}
  }

  setIsOpen(courseName: string, isOpen: boolean) {
    userTasksRepo.setWeekPlannerBoxOpen(courseName, isOpen);
  }

  async setTaskDone(task: UserTask, done: boolean) {
    await userTasksRepo.setTaskDoneState(task, done);
  }

  async setSubTaskDone(task: UserTask, subTask: UserSubTask, done: boolean) {
    await userTasksRepo.setSubTaskDoneState(task, subTask.id, done);
  }

  async copyFromPreviousWeek() {
    const { userTasks, openCourses, weekDate } = this.state;
    const prevWeekNo = getAdjustedWeek (moment(weekDate).add(-1, 'week'));
    const prevKEDWeek = KEDWeek(moment(weekDate).year(), prevWeekNo);
    // Read last week's goals and tasks
    let prevTasks = await env.kedBackendClient.list<UserTask>("usertasks", {
      from: prevKEDWeek.notBefore,
      to: prevKEDWeek.notAfter,
      role: "USER",
      include: ["task", "course", "acl"],
      flags: ["includeIdsOnly"]
    });
    prevTasks = prevTasks
      .filter(prevTask => !prevTask.done)  // Don't copy done tasks
      .filter(prevTask => !!prevTask.name) // Don't copy empty learning goals
      .filter(prevTask => !userTasks.some(taskOfCurrentWeek =>
        taskOfCurrentWeek.name === prevTask.name &&
        taskOfCurrentWeek.learningGoal === prevTask.learningGoal &&
        taskOfCurrentWeek.courseName === prevTask.courseName)); // Ignore identical tasks (already copied)

    // Now create copies but place them in the current week.
    let latestTimeStamp = Math.max.apply(Math.max, [weekDate].concat(userTasks.map(t => t.dateTime)));

    const copies = prevTasks.sort(compareProp("dateTime")).map(task => {
      const copy: UserTask = { ...task, dateTime: latestTimeStamp += 1000 };
      copy.id = createUUID();
      if (copy.subTasks) {
        copy.subTasks = copy.subTasks.filter(st => !st.done);
      }
      delete copy.$etag;
      return copy;
    });

    // Store it
    await userTasksRepo.insert(copies);
  }

  render() {
    const weekNumber = moment(this.state.weekDate).week();
    //console.log("Weekydate: "  + new Date(this.state.weekDate) + " which is week no " + weekNumber);
    const currentWeek = moment().week();
    const taskSets = refine(this.state.userTasks);
    const { dialogs, weekTextsUT, strategy, assessment, isLoading } = this.state;
    const isSaving = weekTextsUT && (weekTextsUT.$meta === 'adding' || weekTextsUT.$meta === 'updating');
    const isStrategyEdited = !!weekTextsUT && (strategy !== weekTextsUT.weekTexts.strategy);
    const isAssessmentEdited = !!weekTextsUT && (assessment !== weekTextsUT.weekTexts.assessment);
    const enableSaveButton = !isSaving && (isStrategyEdited || isAssessmentEdited);
    return <div>
      <div className={`ked_boxed weekPlanner${weekNumber === currentWeek ? " currentWeek" : ""}`}
        onKeyDown={ev => {
          if (ev.which === 83 && ev.ctrlKey) {
            ev.preventDefault();
            if (!isSaving && this.isWeekTextsEdited()) {
              this.weekTextsSavingJob.triggerChange(0);
            }
          }
        }}>
        <div className={`weekSelect`}>
          <div className="horizontalItem">
            <p>{L`Vecka ${weekNumber}`}</p>
          </div>
          <div className="horizontalItem">
            <div className="btn-group">
              <button className="btn" onClick={() => this.prevWeek()}>
                <i className="fa fa-angle-left" aria-hidden="true"></i>
              </button>
              <button className="btn" onClick={() => this.nextWeek()}>
                <i className="fa fa-angle-right" aria-hidden="true"></i>
              </button>
            </div>
          </div>
        </div>

        <h4>{L`Loggbok`}</h4>
        {taskSets.length > 0 && <hr />}

        {taskSets.map(props =>
          <UserTasksBox
            key={props.courseName}
            {...props}
            courseName={props.courseName}
            learningGoals={props.learningGoals}
            viewCourseUrl={this.props.viewCourseUrl}
            openCourses={this.state.openCourses}
            addOwnTask={(courseName: string, learningGoalName: string) => this.openAddOwnTaskDialog(courseName, learningGoalName)}
            setIsOpen={this.setIsOpen.bind(this)}
            setTaskDone={this.setTaskDone.bind(this)}
            setSubTaskDone={this.setSubTaskDone.bind(this)}
            editTask={task => this.editTask(task)}
            editSubTask={(task, subTask) => this.editSubTask(task, subTask)}
            removeLearningGoal={lg => this.removeLearningGoal(lg)} />)}

        <hr />

        {isLoading ? <Spinner /> : <div>
          <div className="btn" onClick={ev => this.openAddGoalDialog()}>
            <i className="fa fa-list-alt" aria-hidden="true"></i> {L`Eget lrandeml`}

          </div>
          <div className="btn" onClick={ev => this.copyFromPreviousWeek()}>
            <i className="fa fa-clone" aria-hidden="true"></i> {L`Kopiera ej klara frn fregende vecka`}
          </div>
          <OpenCloseBox
            title={<h5>{L`Strategi & Utvrdering`}</h5>}
            headerOpen={this.state.openCourses["StratUtv"]}
            onOpenClose={becameOpen => this.setIsOpen("StratUtv", becameOpen)}
          >
            <h3>Strategi</h3>
            <p>Hur jag ska gra fr att lra mig.</p>
            <hr/>
            <textarea
              className="weekplanner-textarea"
              value={strategy}
              onChange={ev => {
                this.setState({strategy: ev.target.value});
                this.weekTextsSavingJob.triggerChange(500);
              }} />

            <h3>Utvrdering</h3>
            <p>Reflektion kring din arbetsinsats och dina valda strategier under veckan.
              Utvrdera i frhllande till dina ml.</p>
            <hr/>
            <textarea
              className="weekplanner-textarea"
              value={assessment}
              onChange={ev => {
                this.setState({assessment: ev.target.value})
                this.weekTextsSavingJob.triggerChange(500);
              } }/>
            <div
              className="btn"
              tabIndex={0}
              style={enableSaveButton ? {} : {opacity: 0.5}}
              onClick={()=>!isSaving && this.weekTextsSavingJob.triggerChange(0)}>
                <i className="fa fa-floppy-o" aria-hidden="true" />
                  {enableSaveButton ? " Spara" : " Sparad"}
            </div>
        </OpenCloseBox>

        </div>}
      </div>
      <Dialogs
        dialogs={dialogs}
        popDialog={() => {
            this.setState(
              ({dialogs}) => ({ dialogs: dialogs.slice(0, dialogs.length - 1) }));
          }
        } />
    </div>
  }
}
