
export class PendingJob {
  private timeoutId = null;
  private cancelled = false;
  private pending = false;
  private isJobExecuting = false;
  private jobCallback: ()=>Promise<any>;
  
  constructor(callback: ()=>Promise<any>) {
    this.jobCallback = callback;
  }

  triggerChange(throttle: number): any {
    if (this.cancelled) return;
    this.pending = true;
    if (this.timeoutId !== null)
      clearTimeout(this.timeoutId);
    this.timeoutId = setTimeout(()=>this.launchJob(), throttle);
  }

  stop() {
    if (this.timeoutId !== null)
      clearTimeout(this.timeoutId);
    this.timeoutId = null;
    this.cancelled = true;
  }

  private async launchJob() {
    if (this.cancelled) return;
    if (!this.pending) return;
    if (this.isJobExecuting) return;
    this.timeoutId = null;
    try {
      this.isJobExecuting = true;
      this.pending = false;
      await this.jobCallback();
    } finally {
      this.isJobExecuting = false;
    }
    if (this.pending)
      await this.launchJob();
  }
}
