import moment, { Moment } from "moment";

export function KEDWeek (year: number, week: number) {
  const m = moment(new Date(year, 1, 1)).locale('sv').week(week);
  const res = {
    year,
    week,
    notBefore: m.clone().startOf('week').add(-2, 'days').toDate().getTime(),
    notAfter: m.clone().startOf('week').add(5, 'days').toDate().getTime()
  };
  return res;
}

export function getAdjustedWeek(m: Moment) {
  const clone = m.clone().locale('sv');
  return m.weekday() >= 5 ? // Lrdag 00:00 / Sndag 00:00?
      m.week() + 1 : // Tillhr nsta vecka
      m.week();
}

