import * as React from 'react';
import { Course } from 'kedbackend/models';

type Callback = ()=>void;

interface Props {
  title: string,
  backgroundImage: string,
  tabs: {name: string | JSX.Element, key?: string, link: string}[],
  activeTab: string
}

export const CourseBanner = (props: Props) => {
  const { title, backgroundImage, tabs, activeTab } = props;

  return <div className="sv-row sv-layout sv-skip-spacer">
    <div className="sv-layout sv-skip-spacer sv-column-12">
      <div className="sv-script-portlet sv-portlet sv-skip-spacer">
        {backgroundImage && <style>{`
        .pageHeader {
          background-image: url('${backgroundImage}') !important;
        }
      `}</style>}
        <div className="pageHeader">
          <a>
            <h1>{title}</h1>
          </a>
          <div className="buttonsField">
            <div className="buttonsContainer">
              <div className="align-horizontal"></div>
              <div className='horizontalMenu'>
                <ul>
                  {tabs.map(({name, key, link}, idx) => {
                    const isActive = activeTab === link;
                    return <li key={key || (typeof name === 'string' ? name : idx)} className={isActive ? "activePage" : ""}>
                      <a href={link}>{name}</a>
                    </li>
                  })}
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
}
