import {CourseModule, Course} from 'kedbackend/models';

export default function getIncludedIds (module: CourseModule, course: Course) {
  const result = {};
  course.tasks.filter(t => module.taskIds.indexOf(t.id) !== -1).forEach(task => {
    task.abilities.forEach(({id}) => result[id] = true);
    task.centralContent.forEach(({id}) => result[id] = true);
    task.knowledgeRequirements.forEach(({id}) => result[id] = true);
  });
  return result;
}
