import { Repo, RepoCommunicator} from './repo';
import { KedBackendClient, ListOptions, BatchRunner, createUUID } from 'kedbackend/client';
import { Document} from 'kedbackend/models';

export interface KedRepoOptions<T extends Document> {
  getClient: ()=>KedBackendClient;
  getQueryOptions: ()=>Promise<ListOptions>;
  optimistic: boolean;
  table: string;
  user: string;
}

export class KedRepo<T extends Document> {
  mem: Repo<T>;
  options: KedRepoOptions<T>;
  
  constructor(options: KedRepoOptions<T>) {
    const {table, getClient, getQueryOptions} = options;
    this.mem = new Repo<T>({query: async ()=>{
      const queryOptions = await getQueryOptions();
      return await getClient().list<T>(table, {
        ...queryOptions,
        cacheBust: this.getCacheBust()});
    }});
    this.options = options;
  }

  private getCacheBust() {
    const {table, user} = this.options;
    const cacheBust = localStorage.getItem('cache-bust-' + table + '-' + user);
    return cacheBust || this.regenerateCacheBust();
  }

  private regenerateCacheBust() {
    const {table, user} = this.options;
    const cacheBust = createUUID();
    localStorage.setItem('cache-bust-' + table + '-' + user, cacheBust);
    return cacheBust;
  }

  async upsert(item: T, updater: (item: T) => void) {
    if (!item.$etag) {
      // Items without $etag cannot be retrieved from server, so it should be inserted.
      const updatedItem = Object.assign({}, item);
      updater(updatedItem);
      return await this.insert([updatedItem]);
    } else {
      // We have an $etag, so we can expect it to live on the server.
      // However, take care of the unlikely situation that it was deleted from server,
      // and if so, insert it again.
      await this.update([item], updater).catch(e => {
        if (e.name === "http404" || e.name === "http403") {
          const updatedItem = Object.assign({}, item);
          updater(updatedItem);
          return this.insert([updatedItem]);
        }
        return Promise.reject(e);
      });
    }
  }

  async update(items: T[], updater: (item: T) => void) {
    const {getClient, optimistic, table} = this.options;
    const client = getClient();
    const modifiedItems: T[] = items.map(item => {
      const memRepoItem = this.mem.items.find(it => it.id === item.id);
      item = Object.assign({}, memRepoItem || item);
      updater(item);
      return item;
    });
    if (optimistic) await this.mem.update(modifiedItems.map(x => Object.assign({}, x, {$meta: 'updating'})));
    const res = await client.do (br => modifiedItems.forEach(item => br.put(table, item)))
    .catch(e => e.name === "http409" ? // conflict
      // Get a fresh version from server:
      client.list<T>(table, {ids: items.map(item => item.id)}, {cache: 'no-cache'}).then(freshItems => {
        // Update local version:
        const modifiedItems = freshItems.map(freshItem => {
          // Clone the fresh item
          const modified = Object.assign({}, freshItem);
          // Re-run the updater on the clone:
          updater(modified);
          return modified;
        });
        // Re-do the the put operation towards the server.
        return client.do(br => modifiedItems.forEach(item => br.put(table, item)));
      }) :
      // Other unexpected error:
      Promise.resolve(optimistic &&
        this.mem.update(items)) // Undo optimistic update
      .then(()=>{
        return Promise.reject(e); // Reject with the error no matter.
      }));
    this.regenerateCacheBust();
    modifiedItems.forEach(item => {
      item.$etag = res.newEtags[item.id];
      item.$meta = undefined;
    });
    await this.mem.update(modifiedItems); // Ensures new etag is is applied on next action.
  }

  stripGraphs (items: T[], graphs: string[]) {
    return items.map(item => {
      const clone = Object.assign({}, item);
      graphs.forEach(graph => {
        if (item[graph]) {
          clone[graph] = (item[graph] as {id:string}[]).map (doc => ({id: doc.id}));
        }
      });
      return clone;
    });
  }

  async insert(items: T[]) {
    const {getClient, optimistic, table, getQueryOptions} = this.options;
    const client = getClient();
    const queryOptions = await getQueryOptions();
    const graphs = [].concat(queryOptions.include);
    // Give IDs to each item:
    items = items.map(item => item.id ? item : Object.assign({}, item, {id: createUUID()}));
    const stripped = this.stripGraphs(items, graphs);
    if (optimistic) await this.mem.insert(stripped.map(x => Object.assign({}, x, {$meta: 'adding'})));
    const br = new BatchRunner();
    for (const item of stripped) {
      // Also add links to all foreign related items:
      for (const graph of graphs) {
        const foreignItems: Document[] = item[graph];
        if (foreignItems) {
          foreignItems.forEach(doc => {
            br.link2(table, item.id, graph, doc.id);
          });
        }
      }
      br.add(table, item);
    }
    const res = await client.batch(br.mutationRequests).catch(e => {
      if (optimistic) this.mem.delete(items.map(item => item.id));
      return Promise.reject(e);
    })
    this.regenerateCacheBust();
    items.forEach(item => item.$etag = res.newEtags[item.id]);
    if (optimistic)
      await this.mem.update(items); // Update with new $etag.
    else
      await this.mem.insert(items);
  }

  async delete (ids: string[]) {
    const {getClient, optimistic, table} = this.options;
    const client = getClient();
    if (optimistic) await this.mem.update(ids
      .map(id => this.mem.items.find(x => x.id === id))
      .filter(x => x)
      .map(x => Object.assign({}, x, {$meta: 'deleting'})));
    const res = await client.do (br => ids.forEach(id => br.delete(table, id))).catch(async e => {
      if (optimistic) {
        await this.mem.update(ids
          .map(id => this.mem.items.find(x => x.id === id))
          .filter(x => x)
          .map(x => {
            x = Object.assign({}, x);
            delete x.$meta;
            return x;
          }));
      }
      throw e;
    });
    this.regenerateCacheBust();
    await this.mem.delete(ids);
  }
}
