import * as React from 'react';
import { TextInput } from '../utility-components/form-field-text-input';
import { TextAreaFormField } from '../utility-components/form-field-textarea';

interface Props {
  isTask: boolean
  onSave: (name: string, description: string, url: string) => void;
}

interface State {
  name: string;
  description: string;
  url: string;
}

export class AddCustomTask extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props)
    this.state = {
      name: '',
      description: '',
      url: ''
    }
  }

  render() {
    const { name, description, url } = this.state;
    const { isTask, onSave } = this.props;

    return <div>
      <div className="sv-html-portlet sv-portlet sv-skip-spacer">
        <h2>Lgg till eget arbetsml</h2>
        <hr />
        <TextInput
          label="Namn"
          id="AddCustomTask:name"
          placeholder="Ange namn..."
          value={name}
          autoFocus
          onChange={name => this.setState({name})} />

        <TextAreaFormField
          rows={7}
          label="Beskrivning"
          id="AddCustomTask:description"
          placeholder="Lgg till en beskrivning..."
          value={description}
          onChange={description=>this.setState({description})} />

        <TextInput
          label="Lnk"
          id="AddCustomTask:url"
          placeholder="http://www..."
          value={url}
          onChange={name => this.setState({url})} />

      </div>
      <div className="sv-html-portlet sv-portlet">
        <button
          tabIndex={1}
          className="btn btn-large btn-default"
          onClick={() => onSave(name, description, url)}>Spara</button>
      </div>
    </div>
  }
}

