import { KedRepo } from './ked-repo';
import { UserHiddenCourse, School, Course } from 'kedbackend/models';
import env from '../globals/KED.env';
import { EDSCourse } from '../contracts/eds';
import { showError, flatten, compareProp, arrayToMap } from '../utils/utils';
import { createUUID } from 'kedbackend/client';
import { EDSPeriod } from '../apis/edsclient';
import { getSchoolMoment } from '../utils/school-moment';
import moment from 'moment';
export let hiddenCoursesRepo = env.hiddenCoursesRepo as HiddenCoursesRepo;


export interface CourseWithHiddenState {
  id: string;
  name: string;
  description?: string;
  visible: boolean;
  defaultVisible: boolean;
}

export type HiddenCoursesSubscriber = (courses: CourseWithHiddenState[]) => void;

export interface SubscriberOptions {
  fullCourse: boolean;
}

class HiddenCoursesRepo {
  result: CourseWithHiddenState[] | null = null;
  subscribers: {subscriber: HiddenCoursesSubscriber, options: SubscriberOptions}[] = [];
  fullCourse: boolean = false;
  kedCoursesPromise: Promise<Partial<Course>[]>;
  edsCoursesPromise: Promise<EDSCourse[]>;
  userHiddenCoursesPromise: Promise<UserHiddenCourse[]>;
  kedRepo: KedRepo<UserHiddenCourse>;

  constructor() {
    this.kedRepo = new KedRepo<UserHiddenCourse>({
      getClient: () => env.kedBackendClient,
      optimistic: true,
      table: "userhiddencourses",
      user: env.currentUser ? env.currentUser.mail : "",
      getQueryOptions: async () => ({role: "USER"}),
    });

    const initPromise = this.init();

    Promise.all([
      initPromise,
      this.kedCoursesPromise,
      this.edsCoursesPromise
    ]).catch(err => showError(err));
  }

  async init() {
    // Wait till we have a currentUser that is accurate also for impersonated users.
    const bearerPromise = env.bearerProvider ? env.bearerProvider.getBearer() : Promise.resolve({});

    // In parallell, we request:
    //  1) KEDBackend: Schools.activeCourses
    //  2) EDS.getActiveAcourses()
    //  3) (via subscription): KEDBackend: userHiddenCourses

    this.kedCoursesPromise = bearerPromise.then(()=>this.listKedCourses());
    this.edsCoursesPromise = bearerPromise.then(()=>this.listEDSCourses());
    let resolveUserHiddenCoursesPromise: (x: UserHiddenCourse[])=>void;
    let userHiddenCoursesResolved = false;
    this.userHiddenCoursesPromise = new Promise(resolve => resolveUserHiddenCoursesPromise = x => {
      if (userHiddenCoursesResolved) {
        this.userHiddenCoursesPromise = Promise.resolve(x);
      } else {
        userHiddenCoursesResolved = true;
        resolve(x);
      }
    });
    await bearerPromise;

    this.kedRepo.mem.subscribe(async userHiddenCourses => {
      // Change or initial value ready to be notified out.
      // But first, check we have all the related data responses we need.
      const [activeCourses, edsActiveCourses] = await Promise.all([
        this.kedCoursesPromise, 
        this.edsCoursesPromise]);
      
      // Refine the three results into a single result
      this.result = await this.createCoursesList(edsActiveCourses, userHiddenCourses, activeCourses);
      // Notify our subscribers:
      this.subscribers.forEach(subscriber => this.notifySubscriber(subscriber.subscriber, subscriber.options));
      resolveUserHiddenCoursesPromise(userHiddenCourses);
    });
  }

  async listKedCourses() {
    const fullCourse = this.fullCourse;
    console.log("FullCourse: "+ fullCourse);
    const schools = await env.kedBackendClient.list<School>('schools', {
      role: "USER", // Workaround from showing activated templates among courses of the school
      name: env.currentUser.school,
      include: "activeCourses",
      flags: fullCourse ? [] : ["includeIdsAndNamesOnly"],
      cacheBust: env.currentUser.username
    });
    const activeCourses = flatten(schools.map(school => school.activeCourses)).sort(compareProp("name"))
    return activeCourses;
  }

  listEDSCourses(): Promise<EDSCourse[] | null> {
    const periodName = new EDSPeriod(getSchoolMoment(moment())).period;
    return env.currentUser.roles.indexOf('EMPLOYEE') === -1 ?
      // STUDENTs should, by default, only show courses that is listed in EDS
      env.edsClient.getActiveCourses({periodName}) :
      // EMPLOYEEs should, by default, show all courses on school - no need to query EDS
      null;
  }

  async createCoursesList(
    edsActiveCourses: EDSCourse[] | null,
    userHiddenCourses: UserHiddenCourse[],
    activeCourses: Partial<Course>[]) : Promise<CourseWithHiddenState[]>
  {
    const hiddenCoursesMap = arrayToMap(userHiddenCourses.filter(hc => !hc.show), hc => hc.name);
    const visibleCoursesMap = arrayToMap(userHiddenCourses.filter(hc => hc.show), hc => hc.name);

    const isStudent = env.currentUser.roles.some(role => role === 'STUDENT');

    const edsCourseMap = edsActiveCourses ?
      arrayToMap(edsActiveCourses, c => c.name.substr(0, 20)) :
      {};

    const result = await Promise.all(activeCourses.map(async ({id, name, description, modifiedBy}) => {

      const edsCourse = edsCourseMap[name.substr(0, 20)];

      const defaultVisible = isStudent ?
        edsCourse != null : // Course name also listed in EDS
        true; // Teachers not dependent on EDS listing

      const visible = defaultVisible ?
        !hiddenCoursesMap[id] : // Visible unless user has overridden that.
        !!visibleCoursesMap[id]; // Hidden unless user has overridden that.
      
      if (!this.fullCourse && name.length >= 20) {
        // name could be truncated. Resolve the full name.
        if (edsCourse) {
          // Resolve full course name from EDS.
          name = edsCourse.name;
        } else if (visible) {
          // No EDS to fallback to. Unless we wanna print course name as "THE Partial name" + "...",
          // we must query the full course here from kedbackend:
          const fullCourse = await env.kedBackendClient.get("courses", id);
          name = fullCourse.name;
        }
      }

      // Resolve description:
      if (modifiedBy && modifiedBy.name && !description) {
        description = `${modifiedBy.name}s version`;
      }

      return {
        id,
        name,
        description,
        visible,
        defaultVisible
      };
    }));

    return result;
  }

  async hideCourse(c: CourseWithHiddenState) {
    const overrides = this.kedRepo.mem.items.filter(hc => hc.name === c.id);
    if (c.defaultVisible) {
      await this.kedRepo.insert([{
        id: createUUID(),
        name: c.id
      }]);
    } else {
      await this.kedRepo.delete(overrides.map(ov => ov.id));      
    }
  }

  async showCourse(c: CourseWithHiddenState) {
    const overrides = this.kedRepo.mem.items.filter(hc => hc.name === c.id);
    if (c.defaultVisible) {
      await this.kedRepo.delete(overrides.map(ov => ov.id));
    } else {
      await this.kedRepo.insert([{
        id: createUUID(),
        name: c.id,
        show: true
      }]);
    }
  }

  notifySubscriber = (subscriber: HiddenCoursesSubscriber, options: SubscriberOptions) => {
    try {
      subscriber(options.fullCourse ? this.result : this.result.filter(c => c.visible));
    } catch (err) {
      console.error(err);
    }
  }

  async subscribe (subscriber: HiddenCoursesSubscriber, options: SubscriberOptions) {
    if (!this.fullCourse && options.fullCourse) {
      // The subscriber demands full courses
      // Need to query that before notifying
      // Also affect state for future internal notification
      this.fullCourse = options.fullCourse;
      this.kedCoursesPromise = this.listKedCourses(); // Redo this call, now loading full courses
      // Wait for all outstanding fetch() calls to complete:
      const [activeCourses, edsActiveCourses, userHiddenCourses] = await Promise.all([
        this.kedCoursesPromise, 
        this.edsCoursesPromise,
        this.userHiddenCoursesPromise]);
      // Assemble result:
      this.result = await this.createCoursesList(edsActiveCourses, userHiddenCourses, activeCourses);
    }
    await this.userHiddenCoursesPromise; // So we know that this.result is there.
    this.notifySubscriber(subscriber, options);
    this.subscribers.push({subscriber, options});
  }

  unsubscribe (subscriber: HiddenCoursesSubscriber) {
    this.subscribers = this.subscribers.filter(s => s.subscriber !== subscriber);
  }
}

if (!hiddenCoursesRepo) {
  hiddenCoursesRepo = env.hiddenCoursesRepo = new HiddenCoursesRepo();
}
