import { UserTask } from 'kedbackend/models';
import { UserTasksBoxProps } from './user-tasks-box';
import { arrayToMap, arrayToLookup, compareProp, L } from "../../utils/utils";

export interface VirtualLearningGoal {
  name: string;
  allTasks: UserTask[];
  step?: number;
  url?: string;
  tasks: UserTask[];
}

export function refine(tasks: UserTask[]) {
  const result = [] as Partial<UserTasksBoxProps>[];
  const mapper = {} as { [name: string]: UserTasksBoxProps };
  const tasksPerCourse = arrayToLookup(tasks, t => t.courseName || '');
  for (const courseName of Object.keys(tasksPerCourse).sort().filter(x => x).concat(tasksPerCourse[''] ?
    [''] : [])) {
    const courseTasks = tasksPerCourse[courseName] || tasksPerCourse[''];
    const tasksPerLearningGoal = arrayToLookup(courseTasks, t => t.learningGoal);
    const resultLearningGoals: VirtualLearningGoal[] = [];
    for (const learningGoal of Object.keys(tasksPerLearningGoal)) {
      const lgTasks = tasksPerLearningGoal[learningGoal].sort(compareProp("dateTime"));
      const learningGoalTask = lgTasks
        .filter(t => t.name == null) // If name is undefined or null, this represents a learning goal
        [0];
      const url = learningGoalTask && learningGoalTask.url;
      resultLearningGoals.push({
        name: learningGoal,
        allTasks: lgTasks,
        url,
        step: lgTasks.map(t => t.step).filter(step => step)[0],
        tasks: lgTasks.filter(t => t.name)
      });
    }
    result.push({
      courseName,
      learningGoals: resultLearningGoals
    });
  }
  return result;
}
