import * as React from 'react';
import env from '../../../globals/KED.env';
import { Assignment } from 'kedbackend/models';
import { showError, compareProp, compareProps } from '../../../utils/utils';
import { Spinner } from '../../course-builder/sub-components/spinner';
import moment from 'moment';
import { HttpError } from 'kedbackend/client';
import { hasWriteAccess, isTeacherAtSchool } from '../../../access-control';
import download from 'downloadjs';

const MAX_FILE_SIZE_MB = 10;
const ALLOWED_EXTENSIONS = [
  ".pdf",
  ".odt",
  ".docx"
];

interface Props {
  courseId: string;
  taskId: string;
  school: string;
}

interface State {
  assignments: Assignment[];
  loading: boolean;
  uploadingFile?: File;
  error?: string;
}

export class TaskAssignments extends React.Component<Props, State> {

  constructor(props) {
    super(props);
    this.state = {
      assignments: [],
      loading: true
    };
  }

  setStatePromised(cb: (prevState: Readonly<State>, props: Props) => Partial<State>) {
    return new Promise(resolve => super.setState(cb as any, resolve));
  }  

  componentDidMount() {
    this.load();
  }

  async componentWillReceiveProps(nextProps: Props) {
    try {
      if (nextProps.taskId !== this.props.taskId ||
          nextProps.courseId !== this.props.courseId)
      {
        await this.setStatePromised(()=>({
          assignments: [],
          loading: true
        }));
        await this.load();
      }
    } catch (err) {
      showError(err);
    }
  }

  async load() {
    try {
      const client = env.kedBackendClient.http;
      const {courseId, taskId, school} = this.props;
      const teacherAtSchool = isTeacherAtSchool(school);
      const res = await (teacherAtSchool ?
        client.get(`assignments/${courseId}/${taskId}`) :
        client.get(`assignments/${courseId}/${taskId}/${env.currentUser.mail}`));
      if (res.status !== 200) {
        throw new HttpError(res.status, await res.text());
      }

      const assignments = await res.json() as Assignment[];
      this.setState({
        error: null,
        loading: false,
        assignments
      });
    } catch (error) {
      this.setState({
        loading: false,
        error: `Kunde inte ladda inlmningsuppgifter`,
        assignments: []
      });
      console.error(error);
    }
  }

  onDrag (ev: React.DragEvent<HTMLDivElement>) {
    ev.stopPropagation();
    ev.preventDefault();
    ev.dataTransfer.dropEffect = 'copy';
  }
  
  async onDrop(ev: React.DragEvent<HTMLDivElement>) {
    try {
      ev.stopPropagation();
      ev.preventDefault();
      const file = ev.dataTransfer.files[0];
      if (this.state.assignments.some(a =>
        a.user === env.currentUser.mail &&
        a.filename === file.name))
      {
        if (!confirm(`Erstta befintlig fil (${file.name})?`)) {
          return;
        }
      }
      if (this.props.school.toLowerCase() !== env.currentUser.school.toLowerCase()) {
        if (!confirm(`Uppgiften tillhr en annan skola (${this.props.school}).\n`+
          `Vill du nd lmna in ditt arbete hr?`))
        {
          return;
        }
      }
      this.setState({uploadingFile: file});
      await this.upload(file);
      await this.load();
      this.setState({
        uploadingFile: null
      });
    } catch (error) {
      this.setState({
        error: "Kunde inte ladda upp filen. " + (error.message || error),
        uploadingFile: null
      });
      console.error(error);
    }
  }
  
  async upload(file: File) {
    const client = env.kedBackendClient.http;
    const {courseId, taskId} = this.props;
    const {mail} = env.currentUser;
    if (file.size > MAX_FILE_SIZE_MB * 1024 * 1024) {
      throw new Error(`Filen fr inte vara strre n ${MAX_FILE_SIZE_MB} MB`);
    }
    const lowerFilename = file.name.toLowerCase();
    if (!ALLOWED_EXTENSIONS.some(ext => lowerFilename.endsWith(ext))) {
      throw new Error(`Filens ndelse mste vara ${ALLOWED_EXTENSIONS.join(' / ')}`);
    }
    const body = new FormData();
    body.append('files', file);
    const res = await env.kedBackendClient.http.fetch(
      `assignments/${courseId}/${taskId}/${mail}`,
      'put',
      {},
      {userDisplayName: env.currentUser.displayName},
      {
        body
      });
    if (res.status !== 200) {
      throw new HttpError(res.status, await res.text());
    }
  }

  async download(file: Assignment) {
    try {
      const res = await env.kedBackendClient.http.fetch(file.mediaLink, 'get', {});
      if (res.status >= 400) {
        throw new HttpError(res.status, await res.text());
      }
      const blob = await res.blob();
      download(blob, file.filename, file.contentType);
    } catch (err) {
      this.setState({error: "Kunde inte ladda ned filen. " + (err.message || err)});
      console.error(err);
    }    
  }

  async delete(url: string) {
    try {
      await this.setStatePromised(({assignments}) => ({assignments: assignments.map(a => a.mediaLink === url ?
        {...a, metadata: {...a.metadata, $meta: 'delete'}} : {...a})}));
      const res = await env.kedBackendClient.http.fetch(url, 'delete', {});
      if (res.status >= 400) {
        throw new HttpError(res.status, await res.text());
      }
      await this.load();
    } catch (err) {
      this.setState({error: "Kunde inte ta bort filen. " + (err.message || err)});
      console.error(err);
    }
  }

  render() {
    const {courseId, taskId, school} = this.props;
    const {loading, assignments, error, uploadingFile} = this.state;
    const teacherAtThisSchool = isTeacherAtSchool(school);
    let files = [...assignments];
    if (uploadingFile) {
      const virtualFile: Assignment = {
        courseId,
        taskId,
        filename: uploadingFile.name,
        size: uploadingFile.size,
        user: env.currentUser.mail,
        contentType: uploadingFile.type,
        uploadDate: Date.now(),
        mediaLink: `assignments/${courseId}/${taskId}/${env.currentUser.mail}/${uploadingFile.name}`,
        metadata: {$meta: 'add'} as {[key: string]: string}
      };
      files = files.filter(file => file.mediaLink !== virtualFile.mediaLink);
      files = [...files, virtualFile];
    }
    files = files.sort(compareProps(["user", "filename"], ["sv", "en"], {sensitivity: 'base'}));
    
    return (
      <div onDragOver={ev => this.onDrag(ev)} onDrop={ev=>this.onDrop(ev)}>
        {error ? <>
          <p className="error">{error}</p>
        </>: undefined}
        {loading ? <>
          <Spinner /> Laddar inlmnade filer...
        </> : files.length === 0 ?
          teacherAtThisSchool ?
            <p>Ingen elev har lmnat in ngot arbete nnu.</p> :
            undefined
        : <>
          <table className="task-assignments-table"><thead><tr>
            {teacherAtThisSchool ? <th>Elev</th> : undefined}
            <th>Filnamn</th>
            <th>Inlmnat den</th>
            <th>Status</th>
            <th />
          </tr></thead><tbody>{
            files.map(file => <tr
              key={file.mediaLink}
              style={file.metadata.$meta ? {opacity: 0.5}: {}}
            >
              {teacherAtThisSchool ? <td>
                <a href={`mailto:${file.user}`}>
                  {file.metadata.userDisplayName || file.user}
                </a>
              </td> : undefined}
              <td><a href="#" onClick={ev => {
                ev.preventDefault();
                ev.stopPropagation();
                this.download(file);
              }}>{file.filename}</a></td>
              <td>{moment(file.uploadDate).format('YYYY-MM-DD HH:mm')}</td>
              <td>{file.metadata.$meta === 'add' ?
                <><Spinner /> Laddar upp...</>:
                file.metadata.$meta === 'delete' ?
                <><Spinner /> Tar bort...</> :
                <><i className="fa fas fa-check" /> Inlmnad</>
              }</td>
              <td>{!file.metadata.$meta && file.user === env.currentUser.mail &&
                <a href="#" onClick={ev => {
                  ev.stopPropagation();
                  ev.preventDefault();
                  this.delete(file.mediaLink);
                }}><i className="fa fa-trash"/></a>}</td>
            </tr>)
          }</tbody></table>
        </>}
        {teacherAtThisSchool ? undefined : <div className="drop-zone task-assignment-drop-zone">
            Droppa ditt frdiga arbete hr<br/>
            <b>(.pdf, .docx, .odt)</b>)
          </div>}
      </div>
    );

  }
}