import * as React from 'react';
import { L, arrayToLookup } from '../../../utils/utils';
import { Course, CourseModule, Task } from 'kedbackend/models';
import { KnowledgeRequirementsTable } from "../subcomponents/knowledge-requirements-table";
import getIncludedIds from '../utils/get-included-ids';
import { AbilitiesBox } from "../subcomponents/abilities-box";
import { CentralContentBox } from "../subcomponents/central-content-box";
import { LearningGoalsList } from '../subcomponents/learning-goals-list';
import { TaskList } from '../subcomponents/task-list';
import { getCustomizedTask } from './get-customized-task';

// Views

interface Props {
  course: Course;
  module: CourseModule;
}

interface State {
}

export class CourseModulePage extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
    }
  }

  render() {
    const { module, course } = this.props;
    const incluedIds = getIncludedIds(module, course);
    const learningTasks = module.taskIds
      .map(id => course.tasks.find(t => t.id === id))
      .filter(task => !!task)
      .map(task => ({
        id: task.id,
        task: getCustomizedTask(module, task),
        course,
        name: task.name,
        url: `#${encodeURIComponent(module.name.trim())}/${task.id}`, // Frsttsbladets URL
        courseName: course.name,
        learningGoal: task.learningGoal || module.name,
      }));
    const assignmentTasks = learningTasks.filter(t => t.task && t.task.assignment);
    const nonAssignmentTasks = learningTasks.filter(t => !t.task || !t.task.assignment);
    const commonTasks = nonAssignmentTasks.filter(t => !t.task.learningGoal);
    const learningGoals = arrayToLookup(
      nonAssignmentTasks.filter(t => !!t.task.learningGoal),
      t => t.learningGoal);
    return <div>
      <div className="sv-text-portlet sv-use-margins sv-skip-spacer">
        <div className="sv-text-portlet-content">
          <h1 className="h1">{module.name}</h1>
        </div>
      </div>
      <div className="sv-vertical sv-layout">
        <div dangerouslySetInnerHTML={{ __html: module.mandatoryContent }}>
        </div>
      </div>

      <div className="sv-html-portlet sv-portlet">
        <div className="ked_boxed">
          <h3>Modulens kunskapskrav, frmgor och centrala innehll</h3>
          {(!!module.startWeek || !!module.endWeek) && <div className="week-period-div">
            <div>Startvecka</div>
            <div>{module.startWeek || "--"}</div>
            <div>Slutvecka</div>
            <div>{module.endWeek || "--"}</div>
            <br />
          </div>}
          <hr />

          <KnowledgeRequirementsTable
            title="I den hr modulen bedms fljande kunskapskrav"
            requirements={course.knowledgeRequirements}
            includedIds={incluedIds} />

          <AbilitiesBox
            title="Den hr modulen behandlar fljande frmgor"
            abilities={course.abilities.filter(a => incluedIds[a.id])} />

          <CentralContentBox
            title="Den hr modulen behandlar fljande centrala innehll"
            centralContent={course.centralContent.filter(c => incluedIds[c.id])} />

        </div>
      </div>

      {module.taskIds.length > 0 && <div className="sv-script-portlet sv-portlet">
        <div className="ked_boxed">
          {assignmentTasks.length === 0 ? undefined : <>
            <h2>Inlmningsuppgifter</h2>
            <hr />
            <TaskList learningTasks={assignmentTasks} />
          </>}
          {nonAssignmentTasks.length === 0 ? undefined : <>
            <h2>Uppgifter</h2>
            <hr />
            <LearningGoalsList
              commonTasks={commonTasks}
              learningGoals={Object.keys(learningGoals).map(learningGoal => ({
                name: learningGoal,
                learningTasks: learningGoals[learningGoal]
              }))} />
            </>}
        </div>
      </div>}
      {module.resources.length > 0 && <div className="sv-html-portlet sv-portlet">
        <div className="ked_boxed">
          <h2>Resurser</h2>
          <hr />
          <div className="taskContainer">{module.resources.map((r, idx) =>
            <div className="align-horizontal" key={idx}>
              <div className="horizontalItem top">
                <a href={r.url}>{r.name}</a>
              </div>
            </div>)}
          </div>
        </div>
      </div>}
    </div>
  }
}
