import moment from 'moment';
import { localMoment } from '../../globals/moment-sv-locale';

type Moment = moment.Moment;

export class WeekPlannerPersistedState {
  static EXPIRATION_HOURS = 12;
  static VERSION = 2;

  user: string;
  lastWrite: number;
  weekDate: number;
  openCourses: { [path: string]: boolean }

  constructor (userOrCopy: string | WeekPlannerPersistedState) {
    if (typeof userOrCopy === 'string') {
      this.user = userOrCopy;
      this.lastWrite = Date.now();
      this.weekDate = localMoment().startOf('week').valueOf();
      this.openCourses = {};
    } else {
      Object.assign(this, userOrCopy);
    }
  }

  static async load(user: string) {
    const cookie = localStorage.getItem('WeekPlannerPersistedState2');
    const storedData = cookie && (JSON.parse(cookie) as WeekPlannerPersistedState);
    const state = new WeekPlannerPersistedState(user);
    if (storedData) Object.assign(state, storedData);

    return state.user === user && !state.isExpired(moment()) ?
      state : new WeekPlannerPersistedState(user);
  }

  async save() {
    this.lastWrite = Date.now();
    const json = JSON.stringify(this);
    localStorage.setItem('WeekPlannerPersistedState2', json);
  }

  isExpired (asOf: Moment) {
      return moment(this.lastWrite)
        .isBefore(
            asOf.add(
                0-WeekPlannerPersistedState.EXPIRATION_HOURS, 'hours')
        );
  }
}
