import * as React from 'react';
import { L, showError } from '../../../utils/utils';
import { CourseBanner } from "./course-banner";
import { Course, CourseModule, Task } from 'kedbackend/models';
import { parseHashQueryString } from "../../../utils/query-string";
import { CourseModulePage } from './course-module-page';
import { RunUpPage } from './run-up-page';
import { initManualScrollRestoration, restoreScrollPosition } from '../utils/manual-scroll-restoration';
import { hasWriteAccess, preserveImpersonationQuery } from '../../../access-control';
import { WeekPlanner } from "../../weekplanner";
import { TaskPage } from "./task-page";
import { getCustomizedTask } from './get-customized-task';

interface Props {
  course: Course;
  courseBuilderUrl: string;
}

interface State {
  module: CourseModule | null;
  task: Task | null;
}


initManualScrollRestoration();

export class CoursePage extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.onHashChange = this.onHashChange.bind(this);
    const {module, task} = this.getModuleAndTaskFromHash();
    this.state = {
      module,
      task
    };
  }

  getModuleAndTaskFromHash() {
    let split = (location.hash.substr(1) || "").split('/');
    if (split.length > 0 && !split[0]) split = split.slice(1); // Allow leading slash #/module/task
    const [moduleName, taskId] = split.map(part => decodeURIComponent(part));
    let module = this.props.course.modules
      .filter(m => (m.name || "")
        .trim().toLowerCase() === (moduleName || "").trim().toLowerCase())[0] || null;
    let task = taskId ?
      this.props.course.tasks.find(t => t.id === taskId) || null :
      null;
    if (task && !module) {
      // Resolve module by finding the module that holds the task:
      module = this.getOrderedModules()
        .filter(m => m.taskIds
          .some(taskId => task.id === taskId))[0];
    }
    if (task && module) {
      task = getCustomizedTask(module, task);
    }
    return {module, task};
  }

  componentDidMount() {
    window.addEventListener('hashchange', this.onHashChange);
  }

  componentWillUnmount() {
    window.removeEventListener('hashchange', this.onHashChange);
  }

  onHashChange() {
    const {module, task} = this.getModuleAndTaskFromHash();
    this.setState({
      module,
      task
    }, ()=>{
      
      let fallbackScrollPosition = undefined;
      if (task) {
        const taskAnchor = document.getElementById('ked-task-anchor');
        if (taskAnchor) fallbackScrollPosition = (window.pageYOffset + taskAnchor.getBoundingClientRect().top);
        else fallbackScrollPosition = 0;
      }
      restoreScrollPosition({fallbackScrollPosition});
    });
  }

  getOrderedModules() {
    const { course } = this.props;
    const moduleOrder = course.moduleOrder || course.modules.map(m => m.id);
    return moduleOrder
      // Map ordered id to module
      .map(id => course.modules.find(m => m.id === id))
      // Ignore entries that only exists in moduleOrder but not in modules
      .filter(m => !!m)
      // Append modules at end that are not listed in moduleOrder
      .concat (course.modules.filter(m => !moduleOrder.find(id => m.id === id)));
  }  

  render() {
    const {course, courseBuilderUrl} = this.props;
    const {module, task} = this.state;
    const orderedModules = this.getOrderedModules();
    return <div>
      <CourseBanner
        title={course.name + " - " + course.points + "p"}
        backgroundImage={(course.images.find(img => img.id === course.imageId) || {url:''}).url}
        tabs={[{name: "Upptakt" as string | JSX.Element, link: "#", key: "Upptakt"}]
          .concat(orderedModules.map(({ name }) => ({ name, link: "#" + encodeURIComponent(name), key: name })))
          .concat(course.workFlowLink ? [{
            name: "Arbetsgng",
            key: "workflow",
            link: course.workFlowLink
          }] : [])
          .concat(hasWriteAccess(course) ? [{
            name: <span><i className="fa fa-pencil" aria-hidden="true"></i> Redigera denna kurs</span>,
            key: "edit",
            link: `${preserveImpersonationQuery(courseBuilderUrl, {})}#/courses/${course.id}/edit`}] : [])

        }
        activeTab={module ? "#"+encodeURIComponent(module.name) : "#"}
      />
      <div className="sv-row sv-layout"
        //id="ked-task-anchor" // For window.scrollTo to position here!. Skipping this. scroll to top instead!
      >
        <div className="sv-layout sv-skip-spacer sv-column-12">
          <div className="sv-spacer-20pxvt sv-vertical sv-layout sv-skip-spacer">
            <div className="pagecontent sv-layout sv-spacer-20pxvt sv-skip-spacer">
              <div className="sv-row sv-layout sv-skip-spacer">
                <div className="sv-layout sv-skip-spacer sv-column-7">
                  {task ?
                    <TaskPage task={task} course={course} /> :
                   module ?
                    <CourseModulePage course={course} module={module} /> :
                    <RunUpPage course={course} />}
                </div>
                <div className="sv-layout sv-column-5">
                  <div className="sv-vertical sv-layout sv-skip-spacer">
                    <div className="sv-html-portlet sv-portlet sv-skip-spacer">
                      <WeekPlanner viewCourseUrl={''} />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  }
}
