import * as React from 'react';
import { Course, CourseModule, Task, UserTask } from 'kedbackend/models';
import env from '../../../globals/KED.env';
import { WeekPlannerPersistedState } from "../../weekplanner/weekplanner-persisted-state";
import moment from 'moment';
import { createUUID, BatchRunner, DocumentAccess } from 'kedbackend/client';
import { userTasksRepo } from '../../../repos/user-tasks-repo';
import { arrayToMap, L } from '../../../utils/utils';
import { Spinner } from '../../course-builder/sub-components/spinner';
import { Confirmation } from './confirmation';

interface Props {
  learningTasks: LearningModuleTask[]
}

interface State {
  weekDate: number;
  userTasks: UserTask[];
  confirmations: string[];
}

export interface LearningModuleTask {
  id: string;
  name: string;
  url: string;
  /** courseName
   * For KG: courseName
   * For KS: Subject name
   */
  courseName: string;
  /** learningGoal
   * For KG: module name or learning goal of the task
   * For KS/step-course: course name
   * For KS/theme-course: learning goal
   */
  learningGoal: string;
  /** Step
   * Only present in KS/step-course
   */
  step?: number;
  /** task
   * Only for courses built by coursebuilder
   */
  task?: Task;
  /** course
   * Only for courses built by coursebuilder
   */
  course?: Course;
}

export class TaskList extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      userTasks: [],
      weekDate: Date.now(),
      confirmations: []
    }
    this.onChange = this.onChange.bind(this);    
  }

  onChange (userTasks: UserTask[], persisted: WeekPlannerPersistedState) {
    if (persisted.weekDate !== this.state.weekDate) {
      this.setState({
        confirmations: []
      })
    }
    this.setState({
      userTasks,
      weekDate: persisted.weekDate
    });
  }

  componentDidMount() {
    userTasksRepo.subscribe(this.onChange);
  }

  componentWillUnmount() {
    userTasksRepo.unsubscribe(this.onChange);
  }

  async toggleTask (learningTask: LearningModuleTask) {
    const userTasks = this.state.userTasks.filter(ut => {
      return learningTask.task ?
        ut.task && ut.task.some(t => t.id === learningTask.id) :
        ut.siteVisionPageId === learningTask.id;
    });
    const {weekDate} = this.state;
    
    if (userTasks.length === 0) {
      // No task matches this learningModuleTask - not present in weekplanner.
      let latestTimeStamp = Math.max.apply(
        Math.max,
        [weekDate].concat(this.state.userTasks.map(t => t.dateTime)));

      const userTask: UserTask = {
        id: createUUID(),
        courseName: learningTask.courseName,
        dateTime: latestTimeStamp + 2000,
        learningGoal: learningTask.learningGoal,
        name: learningTask.name,
        url: learningTask.url,
        acl: [
          // Default ACL: Let user self have full control over this item:
          new DocumentAccess("email", env.currentUser.mail, "S"),
          // Additional ACL: Let employees on same school have read access to it.
          // This currently only applies to tasks that refer to course tasks (not custom tasks!)
          new DocumentAccess("schoolRole", `${env.currentUser.school}/EMPLOYEE`, "R")
        ].map(ac => ac.toString())
      };
      if (learningTask.task && learningTask.task.deadline) {
        userTask.deadline = learningTask.task.deadline;
      }
      if (learningTask.task && learningTask.course) {
        userTask.task = [learningTask.task];
        userTask.course = [learningTask.course];
      } else {
        userTask.siteVisionPageId = learningTask.id;
      }
      if (learningTask.step) {
        userTask.step = learningTask.step;
      }
      await Promise.all([
        userTasksRepo.setWeekPlannerBoxOpen(learningTask.courseName, true),
        userTasksRepo.insert([userTask])]);
    } else {
      // Already present in weekplanner. It's time to delete those that matched us.
      await userTasksRepo.delete(userTasks.map(t => t.id));
    }
    
  }

  render() {
    const {learningTasks} = this.props;
    const userTasks = arrayToMap(this.state.userTasks, ut => ut.task && ut.task.length > 0 ?
      ut.task.map(t => t.id)[0] : // course-builder tasks looked up by id
      ut.siteVisionPageId ? ut.siteVisionPageId : // non-course-builder tasks
      ut.url);
    const {confirmations} = this.state;
    const weekPlannerWeek = moment(this.state.weekDate).week();
    const currentWeek = moment().week();
    const forWeekString = weekPlannerWeek === currentWeek ? "" :
    weekPlannerWeek === currentWeek + 1 ? L`fr nsta vecka (v${weekPlannerWeek})` :
    weekPlannerWeek === currentWeek - 1 ? L`fr frra veckan (v${weekPlannerWeek})` :
    L`fr vecka ${weekPlannerWeek}`;

    return <div className="taskContainer">
      {learningTasks.map((learningTask, idx) => {
        const taskLookupId = learningTask.id;
        const userTask = userTasks[taskLookupId] || {$meta: 'deleted'} as Partial<UserTask>; // No exist = $meta: 'deleted'
        const isWorking = userTask.$meta === 'adding' || userTask.$meta === 'deleting';
        const selected = userTask.$meta !== 'deleted' && userTask.$meta !== 'deleting';
        const describedAction = selected ?
                  L`Ta bort uppgiften frn egen veckoplanering ${forWeekString}` :
                  L`Lgg till uppgiften i egen veckoplanering ${forWeekString}`;
        const confirmationVisible =
          confirmations.some(tid => taskLookupId === tid);

        return <div key={learningTask.id}><div key={taskLookupId} className="align-horizontal">
          <div className="horizontalItem top">
              <div
                className={`selectedBox${selected ? ' selected' : ''}`}
                style={isWorking ? {opacity: 0.5}: {}}
                onClick={()=>!isWorking && weekPlannerWeek === currentWeek ?
                  this.toggleTask(learningTask) :
                  confirmationVisible ? 
                    this.setState({confirmations: confirmations.filter(tid => tid !== taskLookupId)}) :
                    this.setState({confirmations: [...confirmations].concat(taskLookupId)})}
                title={describedAction}>
              </div>
          </div>
          <div className="horizontalItem top">
            <a href={learningTask.url}>{learningTask.name}</a>
          </div>
        </div>
        <Confirmation
            visible={confirmationVisible}
            text={describedAction}
            onConfirm={()=>{
              this.toggleTask(learningTask);
              this.setState({confirmations: confirmations.filter(tid => tid !== taskLookupId)});
            }}
            onCancel={()=>{
              this.setState({confirmations: confirmations.filter(tid => tid !== taskLookupId)});
            }} />
        </div>
        }
      )}
    </div>
  }
}
