import * as React from 'react';

interface Props {
  label: any;
  children: any;
}

function findId(node) {
  const recucheck = new Set();
  return (function findId(node){
    if (typeof node === 'string') return null;
    if (recucheck.has(node)) {
      debugger;
      return;
    }
    recucheck.add(node);
    if (node.props) {
      if (node.props.id) return node.props.id;
      if (node.props.children) {
        return findId(node.props.children);
      }
      return;
    }
    if (node.length) {
      for (let i=0;i<node.length; ++i) {
        const child = node[i];
        if (child) {
          const childId = findId(child);
          if (childId) return childId;
          //console.log(child);
        }
      }
    }
  })(node);
}

export class FormField extends React.Component<Props> {
  constructor (props: Props) {
    super(props);
  }

  render() {
    return <>
        <label className="kclabel" htmlFor={findId(this.props.children)}>{this.props.label}</label>
        {this.props.children}
    </>
  }
}
