import * as React from 'react';
import { L, showError } from '../../utils/utils';
import { ErrorSuccessFeedback } from '../../utils/error-success-feedback';
import env from '../../globals/KED.env';
import { Course } from 'kedbackend/models';
import { loadCourse } from "../course-builder/utils";
import { Spinner } from "../course-builder/sub-components/spinner";
import { CoursePage } from "./course-page";
import { parseQueryString } from "../../utils/query-string";
import { ListCoursesPage } from '../list-courses/list-courses-page';

interface Props {
  courseBuilderUrl?: string;
}

interface State {
  course: Course | null,
  isLoading: boolean
}

export class CourseViewer extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      course: null,
      isLoading: true
    }
  }

  componentWillMount() {
    this.load().catch(showError)
      .then(()=>this.setState({isLoading: false}));
  }

  async load() {
    const courseId = parseQueryString(location.search)["courseId"];
    const course = courseId ? await loadCourse (courseId) : null;
    this.setState({course});
  }

  render() {
    return <div className="course-viewer">
      {this.state.isLoading ?
        <Spinner /> :
        this.state.course ?
          <CoursePage
            course={this.state.course} 
            courseBuilderUrl={this.props.courseBuilderUrl} /> :
          <ListCoursesPage />
      }
      <ErrorSuccessFeedback />
    </div>
  }
}
