import * as React from 'react';
import { Course, School, UserHiddenCourse } from 'kedbackend/models';
import env from '../../globals/KED.env';
import { showError, flatten, compareProp, showInfo, L } from "../../utils/utils";
import { createUUID } from 'kedbackend/client';
import { Spinner } from "../course-builder/sub-components/spinner";
import { parseQueryString, generateQueryString } from "../../utils/query-string";
import { preserveImpersonationQuery } from '../../access-control/index';
import { hiddenCoursesRepo, CourseWithHiddenState } from '../../repos/hidden-courses-repo';

interface Props {
  viewCourseUrl?: string;
}

interface State {
  isLoading: boolean
  courses: CourseWithHiddenState[]
  headerOpen: boolean
}

export class ListCourses extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      isLoading: true,
      courses: [],
      headerOpen: false
    };
    this.updateHiddenCourses = this.updateHiddenCourses.bind(this);
  }

  componentWillMount() {
    hiddenCoursesRepo.subscribe(this.updateHiddenCourses, {fullCourse: true});
  }

  componentWillUnmount() {
    hiddenCoursesRepo.unsubscribe(this.updateHiddenCourses);
  }

  updateHiddenCourses(courses: CourseWithHiddenState[]) {
    this.setState({courses, isLoading: false});
  }

  async hideCourse(course: CourseWithHiddenState) {
    showInfo(L`Sparar...`);
    await hiddenCoursesRepo.hideCourse(course);
    showInfo("");
  }

  async showCourse(course: CourseWithHiddenState) {
    showInfo(L`Sparar...`);
    await hiddenCoursesRepo.showCourse(course);
    showInfo("");
  }

  render() {
    if (this.state.isLoading) return <Spinner />;
    const {courses} = this.state;
    const visibleCourses = courses.filter(course => course.visible);
    const hiddenCourses = courses.filter(course => !course.visible);
    const query = parseQueryString(location.search);
    const viewCourseUrl = this.props.viewCourseUrl || "";

    return <div className="ked_boxed">
      <h3>Skolans kurser</h3>
      <div className="taskContainer odd-even">
        {visibleCourses.map(course =>
          <div className="studentCourse" key={course.id}>
            <div className="align-horizontal">
              <div className="horizontalItem top pull-right">
                <a className="hideItem" onClick={() => this.hideCourse(course)} />
              </div>
              <div className="horizontalItem top">
                <a href={preserveImpersonationQuery(viewCourseUrl, {courseId: course.id})}>{course.name}</a>
              </div>
            </div>
            <div className="horizontalItem top">
              <p className="small">{course.description}</p>
            </div>
          </div>)}
      </div>

      <div className={"openClose" + (this.state.headerOpen ? " open" : "")}>
        <div className="openHeader" onClick={() => this.setState({ headerOpen: !this.state.headerOpen })}>
          <h5>Dolda kurser</h5>
        </div>
        <div className="openContent">
          <div className="taskContainer odd-even">
            {hiddenCourses.map(course => 
            <div className="studentCourse" key={course.id}>
              <div className="align-horizontal">
                <div className="horizontalItem top pull-right">
                  <a className="showItem" onClick={() => this.showCourse(course)}></a>
                </div>
                <a href={generateQueryString({...query, courseId: course.id})}>{course.name}</a>
              </div>
              <div className="horizontalItem top">
                <p className="small">{course.description}</p>
              </div>
            </div>)}
          </div>
        </div>
      </div>
    </div>
  }
}
