import * as React from 'react';
import { UserTask, Task, UserSubTask } from 'kedbackend/models';
import { OpenCloseBox } from '../utility-components/open-close-box';
import { L } from '../../utils/utils';
import { preserveImpersonationQuery } from '../../access-control';
import { courseNameToCssClass } from '../calendar/course-name-to-css-class';
import { getTaskType } from './get-task-type';
import moment from 'moment';
import { VirtualLearningGoal } from './refiner';
import { Features } from '../../features';

export interface UserTasksBoxProps {
  courseName: string;
  learningGoals: VirtualLearningGoal[],
  viewCourseUrl: string;
  openCourses?: { [path: string]: boolean };
  setIsOpen?: (courseName: string, isOpen: boolean) => void;
  setTaskDone?: (task: UserTask, done: boolean) => void;
  setSubTaskDone: (task: UserTask, subTask: UserSubTask, done: boolean) => void;
  addOwnTask?: (courseName: string, learningGoalName: string) => void;
  editTask: (task: UserTask) => void;
  editSubTask: (task: UserTask, subTask: UserSubTask) => void;
  removeLearningGoal: (lg: VirtualLearningGoal)=>void;
};

export class UserTasksBox extends React.Component<UserTasksBoxProps> {
  constructor(props: UserTasksBoxProps) {
    super(props);
  }

  render() {
    const {
      courseName,
      learningGoals
    } = this.props;
    const isOpen = !!this.props.openCourses[courseName];
    const features = new Features();

    return <OpenCloseBox
      title={<h5>{courseName || L`Egna lrandeml`}</h5>}
      headerOpen={isOpen}
      className={courseName && courseNameToCssClass('wp-course-', courseName)}
      onOpenClose={becameOpen => this.props.setIsOpen(courseName, becameOpen)}>
      {learningGoals.map(lg =>
        <div key={lg.name} className="learningGoalContainer">
          {lg.step && <div className="stepIndicator">{lg.step}</div>}
          <div className="align-horizontal">
            <div className="learningGoalText horizontalItem top">{lg.name}</div>
            <div className="horizontalItem">
              &nbsp;<a
                className="trash"
                href="#"
                title="Ta bort lrandemlet och dess uppgifter"
                onClick={ev=>{
                  ev.preventDefault();
                  this.props.removeLearningGoal(lg);
                }}
              >
                <i className="fa fa-trash" />
              </a>
            </div>
          </div>

          <div className="learningGoalTasks">
            <div className="taskContainer">
              {lg.tasks.map(userTask => {
                const isWorking = userTask.$meta === 'adding' || userTask.$meta === 'deleting' || userTask.$meta === 'updating';

                const taskType = getTaskType(userTask);
                const expired = moment(userTask.deadline).startOf('day') < moment().startOf('day');

                return <div key={userTask.id} style={isWorking ? { opacity: 0.5 } : {}}>
                  <div className="align-horizontal">
                    <div className="horizontalItem top">
                      <div className={"checkBox" + (userTask.done ? " checked" : "")}
                        onClick={ev => !isWorking && this.props.setTaskDone(userTask, !userTask.done)}>
                      </div>
                    </div>
                    <div className="horizontalItem top">
                      {taskType === 'courseBuilderTask' ?
                        <a href={getTaskUrl(userTask, this.props.viewCourseUrl)}>{userTask.name}</a> :
                        taskType === 'siteVisionTask' ?
                          <a href={userTask.url}>{userTask.name}</a> :
                          <a
                            className={features.EditUserTasksViaPenOnly && !userTask.url ? "link-less": ""}
                            onClick={features.EditUserTasksViaPenOnly ?
                              undefined :
                              ev=>{
                                ev.preventDefault();
                                this.props.editTask(userTask);
                              }}
                            href={userTask.url}
                            target={(userTask.url+'').toLowerCase().startsWith(location.host.toLowerCase()) ?
                              "_self" :
                              "_blank"}
                          >{userTask.name}</a>}
                      {userTask.deadline && <div className={"dateSet" + (expired ? ' expired' : '')}>
                        <i title={moment(userTask.deadline).format("YYYY-MM-DD")} className="fa fa-calendar" aria-hidden="true"
                          /*style={{cursor: 'pointer'}} */
                          onClick={()=>{}}></i>
                      </div>}
                    </div>
                    {
                      features.EditUserTasksViaPenOnly ?
                      <div className="horizontalItem top taskEdit">
                        <a className="editItem" onClick={() => this.props.editTask(userTask)} />
                      </div> : undefined
                    }
                  </div>
                  {userTask.subTasks && <div className="subtasks">
                    {userTask.subTasks.map(subTask =>
                      <div key={subTask.id} >
                        <div className="horizontalItem top">
                          <div
                            className={"checkBox" + (subTask.done ? " checked" : "")}
                            onClick={ev =>
                              !isWorking &&
                              this.props.setSubTaskDone(userTask, subTask, !subTask.done)}>
                          </div>
                        </div>
                        <div className="horizontalItem top">
                          <a
                            href={subTask.url || undefined}
                            target={(userTask.url+'').toLowerCase().startsWith(location.host.toLowerCase()) ?
                            "_self" :
                            "_blank"}
                            className={
                              subTask.url ?
                                undefined :
                                'link-less'
                          }>{subTask.name}</a>
                        </div>
                        <div className="horizontalItem top taskEdit">
                          <a className="editItem" onClick={() => this.props.editSubTask(userTask, subTask)} />
                        </div>
                      </div>)}
                  </div>}
                </div>
              })}
            </div>
          </div>

          <div className="learningGoalTools">
            <div className="btn addOwnGoal"
              onClick={() => this.props.addOwnTask(this.props.courseName, lg.name)}>
              <i className="fa fa-plus" aria-hidden="true"></i> {L`Eget arbetsml`}
            </div>
          </div>
          <br />
        </div>
      )}
    </OpenCloseBox>
  }
}

function getTaskUrl(userTask: UserTask, viewCourseUrl: string) {
  if (userTask.course && userTask.course.length > 0 && userTask.task && userTask.task.length > 0) {
    const courseUrl = preserveImpersonationQuery(viewCourseUrl, { courseId: userTask.course[0].id });
    return `${courseUrl}#/task/${userTask.task[0].id}`;
  }
  return userTask.url;
}
