import * as React from 'react';
import { Task, Course, UserTask } from 'kedbackend/models';
import { AbilitiesBox } from "../subcomponents/abilities-box";
import { CentralContentBox } from "../subcomponents/central-content-box";
import { KnowledgeRequirementsTable } from "../subcomponents/knowledge-requirements-table";
import { userTasksRepo } from '../../../repos/user-tasks-repo';
import { L } from '../../../utils/utils';
import { createUUID, DocumentAccess } from 'kedbackend/client';
import env from '../../../globals/KED.env';
import moment from 'moment';
import { TaskAssignments } from './task-assignments';
import { isTeacherAtSchool } from '../../../access-control';

interface Props {
  task: Task
  course: Course
}

interface State {
  userTask: UserTask | null | undefined;
  weekPlannerWeek: number;
}

export class TaskPage extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      userTask: undefined,
      weekPlannerWeek: moment().week()
    };
    this.onUserTasksChanged = this.onUserTasksChanged.bind(this);
  }

  componentDidMount() {
    userTasksRepo.subscribe(this.onUserTasksChanged);
  }

  componentWillUnmount() {
    userTasksRepo.unsubscribe(this.onUserTasksChanged);
  }

  componentWillReceiveProps(nextProps: Props) {
    if (nextProps.task.id !== this.props.task.id) {
      const userTask = userTasksRepo.mem.items.find(ut => ut.task && ut.task.length > 0 && ut.task[0].id === nextProps.task.id) || null;
      this.setState({
        userTask,
        weekPlannerWeek: moment(userTasksRepo.persistedState.weekDate).week()
      });
    }
  }

  onUserTasksChanged(userTasks: UserTask[]) {
    const userTask = userTasks.find(ut => ut.task && ut.task.length > 0 && ut.task[0].id === this.props.task.id) || null;
    this.setState({
      userTask,
      weekPlannerWeek: moment(userTasksRepo.persistedState.weekDate).week()
    });
  }

  async toggleTask () {
    const { weekDate } = userTasksRepo.persistedState;
    const { userTask } = this.state;
    const { course, task } = this.props;
    const module = course.modules.find(m => m.taskIds.some(id => id === task.id));
    if (!module) return; // Oh uh!
    if (userTask === undefined) return; // Not yet loaded.
    if (!userTask) {
      let latestTimeStamp = Math.max.apply(
        Math.max,
        [weekDate].concat(userTasksRepo.mem.items.map(t => t.dateTime)));

      const userTask: UserTask = {
        id: createUUID(),
        courseName: course.name,
        dateTime: latestTimeStamp + 2000,
        learningGoal: task.learningGoal || module.name,
        name: task.name,
        url: task.url,
        task: [task],
        course: [course],
        acl: [
          // Default ACL: Let user self have full control over this item:
          new DocumentAccess("email", env.currentUser.mail, "S"),
          // Additional ACL: Let employees on same school have read access to it.
          // This currently only applies to tasks that refer to course tasks (not own tasks!)
          new DocumentAccess("schoolRole", `${env.currentUser.school}/EMPLOYEE`, "R")
        ].map(ac => ac.toString())
      };
      if (task.deadline) {
        userTask.deadline = task.deadline;
      }
      await Promise.all([
        userTasksRepo.setWeekPlannerBoxOpen(this.props.course.name, true),
        userTasksRepo.insert([userTask])]);
    } else {
      await userTasksRepo.delete([userTask.id]);
    }
  }

  render() {
    const { task, course } = this.props;
    const { userTask, weekPlannerWeek } = this.state;
    const abilities = task.abilities.map(a => course.abilities.find(ab => ab.id === a.id)).filter(a => !!a);
    const centralContent = task.centralContent.map(c => course.centralContent.find(cc => cc.id == c.id)).filter(c => !!c);
    const requirements = task.knowledgeRequirements.map(k => course.knowledgeRequirements.find(kr => kr.id === k.id)).filter(k => !!k);
    const isWorking = userTask && (userTask.$meta === 'adding' || userTask.$meta === 'deleting');
    const currentWeek = moment().week();
    const forWeekString = weekPlannerWeek === currentWeek ? "loggboken" :
      weekPlannerWeek === currentWeek + 1 ? L`vald vecka (nsta vecka)` :
      weekPlannerWeek === currentWeek - 1 ? L`vald vecka (frra veckan)` :
      L`vald vecka (v ${weekPlannerWeek})`;
    const deadlineTooLate = task.deadline && moment(task.deadline).endOf('day') < moment();

    return <div>
      <div className="sv-text-portlet sv-use-margins sv-skip-spacer">
        <div className="sv-text-portlet-content">
          <h1 className="h1">{task.name}</h1>
        </div>
      </div>
      <div className="sv-html-portlet sv-portlet">
        {task.content && <>
          <div dangerouslySetInnerHTML={{__html: task.content}} />
        </>}
        {task.learningGoal && <div><h2>Uppgiftens lrandeml</h2>
          <p>{task.learningGoal}</p>
        </div>}
        {task.url && <a href={task.url} className="btn btn-large">G till uppgiften</a>}
      </div>
      <br/>
      {task.assignment ? <>
        <h2>Inlmningsuppgift</h2>
        {isTeacherAtSchool(course.school) ?
          <p>Hr kan du som lrare se elevernas inlmnade arbeten.
          </p> : <>
            <p>Det hr r en inlmningsuppgift. Lmna in ditt arbete som en pdf, doc eller odt fil
              genom att dra det till fltet under.
            </p>
            {task.deadline ? <p><b>
              Den hr uppgiften {deadlineTooLate ? 'skulle varit inlmnad ' : 'skall vara inlmnad '}
              {moment(task.deadline).format('YYYY MM DD')}
            </b></p> : undefined}
            </>}
          <TaskAssignments
            courseId={course.id}
            taskId={task.id}
            school={course.school}
           />
        <br/>
      </> : undefined}
      <div className="sv-html-portlet sv-portlet sv-skip-spacer">
        {abilities.length > 0 && <AbilitiesBox
            title="Den hr uppgiften behandlar fljande frmgor"
            className="larger"
            headerOpen={false}
            abilities={abilities} />}

        {centralContent.length > 0 && <CentralContentBox
            title="Den hr uppgiften behandlar fljande centrala innehll"
            className="larger"
            headerOpen={false}
            centralContent={centralContent} />}

        {requirements.length > 0 && <KnowledgeRequirementsTable
          title="I den hr uppgiften bedms fljande kunskapskrav"
          className="larger"
          headerOpen={true}
          requirements={requirements}
          />}
      </div>      
      <hr/>
      {userTask !== undefined && <div className="taskContainer">
          <div className="align-horizontal">
            <div className="horizontalItem top">
                <div
                  className={`selectedBox${userTask && userTask.$meta !== 'deleting' ? ' selected' : ''}`}
                  style={isWorking ? {opacity: 0.5}: {}}
                  onClick={()=>!isWorking && this.toggleTask()}>
                </div>
            </div>
            <div className="horizontalItem top">
              {userTask ?
                L`Ta bort uppgiften frn ${forWeekString}`:
                L`Lgg till uppgiften i ${forWeekString}`}
            </div>
          </div>
        </div>}
      <hr/>
      {task.resources && task.resources.length > 0 && <div className="sv-html-portlet sv-portlet">
        <h2>Resurser kopplade till uppgiften</h2>
        <div className="taskContainer">
          {task.resources.map((r, idx) =>
            <div key={idx} className="align-horizontal">
              <div className="horizontalItem top">
                <a href={r.url}>{r.name}</a>
              </div>
            </div>
          )}
        </div>
        <br />
      </div>}

 
      <br /><br /><br /><br /><br /><br /><br /><br /><br />
    </div>
  }
}
