import { UserTask } from "kedbackend/models";

export function getTaskType (userTask: UserTask) : 'courseBuilderTask' | 'siteVisionTask' | 'customTask' {
  return userTask.course && userTask.task && userTask.course.length > 0 && userTask.task.length > 0 ?
    'courseBuilderTask' :
    userTask.siteVisionPageId ?
      'siteVisionTask' :
      'customTask';
}
