import * as React from 'react';
import { TextInput } from '../utility-components/form-field-text-input';
import { TextAreaFormField } from '../utility-components/form-field-textarea';
import { UserTask, UserSubTask } from 'kedbackend/models';
import { userTasksRepo } from '../../repos/user-tasks-repo';
import { createUUID } from 'kedbackend/client';

type Props = {
  // Edit Mode
  mode: 'add';
  userTask: UserTask;
  closeDialog;
} | {
  mode: 'edit';
  userTask: UserTask;
  subTask: UserSubTask;
  closeDialog: ()=>void;
}

interface State {
  name: string;
  description: string;
  url: string;
}

export class AddOrEditSubTask extends React.Component<Props,State> {
  constructor (props: Props) {
    super(props);
    if (props.mode === 'edit') {
      const {subTask} = props;
      this.state = {
        name: subTask.name || '',
        description: subTask.description || '',
        url: subTask.url || ''
      };
    } else {  
      this.state = {
        name: '',
        description: '',
        url: ''
      };
    }
  }

  save() {
    // Update repo:
    const {props} = this;
    const {userTask, closeDialog} = props;
    const {name, description, url} = this.state;
    if (props.mode === 'edit') {
      // Edit existing:
      userTasksRepo.update([userTask], ut => {
        const {subTasks} = ut;
        if (subTasks) {
          ut.subTasks = subTasks.map(st =>
            st.id === props.subTask.id ? {...st, name, description, url} :
            {...st}
          );
        }
      });
    } else {
      // Create new task
      const newSubTask = {
        id: createUUID(),
        name,
        description,
        url
      };

      // Update repo:
      userTasksRepo.update([userTask], ut => {
        if (!ut.subTasks) {
          ut.subTasks = [];
        }
        ut.subTasks = ut.subTasks.concat(newSubTask);
      });
    }

    // Close dialog
    closeDialog();    
  }

  delete() {
    // Update repo:
    const props = this.props;  
    if (props.mode !== 'edit') {
      throw new Error("Can only delete in edit mode");
    }
    userTasksRepo.update([props.userTask], ut => {
      if (ut.subTasks) {
        ut.subTasks = ut.subTasks.filter(t => t.id !== props.subTask.id);
      }
    });

    // Close dialog:
    props.closeDialog();
  }

  render() {
    const {name, description, url} = this.state;
    const {props} = this;
    const isEditMode = props.mode === 'edit';
    
    return <>
      <div className="sv-html-portlet sv-portlet sv-skip-spacer">
        <h2>{isEditMode ? 'Redigera underuppgift' : 'Lgg till underuppgift'}</h2>
        <hr />
        <TextInput
          autoFocus
          label="Underuppgiftens namn"
          id="AddUserSubTask:name"
          placeholder=""
          value={this.state.name}
          onChange={name => this.setState({name})}
            />
        <TextAreaFormField
          label="Beskrivning"
          id="AddUserSubTask:description"
          rows={7}
          placeholder=""
          value={this.state.description}
          onChange={description => this.setState({description})}
            />
        <TextInput
          label="Lnk"
          id="AddUserSubTask:url"
          placeholder=""
          value={this.state.url}
          onChange={url => this.setState({url})}
            />
        <br />
      </div>
      <div className="sv-html-portlet sv-portlet">
        {
          props.mode === 'edit' && <button className="btn btn-warning btn-large pull-right"
          onClick={ev => this.delete()}>
          {"Ta bort underuppgift"}</button>}
        <button
          tabIndex={1}
          className="btn btn-large btn-default"
          onClick={()=>this.save()}>Spara</button>
      </div>
    </>
  }
}

