import * as React from 'react';
import { KnowledgeRequirement } from 'kedbackend/models';
import { OpenCloseBox } from "../../utility-components/open-close-box";

interface Props {
  title: string;
  className?: string;
  requirements: KnowledgeRequirement[];
  includedIds?: {[id: string] : true};
  headerOpen?: boolean;
}

interface State {
}

export class KnowledgeRequirementsTable extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.state = {};
  }

  render() {
    const { title, requirements, className, includedIds, headerOpen } = this.props;

    const columns = ["E", "C", "A"];
    const rows: KnowledgeRequirement[][] = [];
    const reqs = requirements.slice();
    let row: KnowledgeRequirement[] | null = null;
    while (true) {
      row = columns.map(grade => {
        const pNext = reqs.findIndex(r => r.gradeStep.toUpperCase() === grade);
        if (pNext < 0) return null;
        const rv = reqs[pNext];
        reqs.splice(pNext, 1);
        return includedIds ?
          (includedIds[rv.id] ? rv : undefined) :
          rv;
      });
      if (row.every(r => r === null))
        break;
      if (!row.every(r => !r)) {
        rows.push(row);
      }
    }

    return <OpenCloseBox title={<p>{title}</p>} className={className} headerOpen={headerOpen}>
      <table>
        <thead>
          <tr>
            {columns.map(c => <th key={c}>{c}</th>)}
          </tr>
        </thead>
        <tbody>
          {rows.map((row,i) =>
            <tr key={i}>
              {row.map((column,j) =>
                <td key={j} dangerouslySetInnerHTML={{__html: column ? column.name : ""}} />
              )}
            </tr>
          )}
        </tbody>
      </table>
    </OpenCloseBox>
  }
}
