import {KedRepo} from './ked-repo';
import { UserTask, UserSubTask } from 'kedbackend/models';
import env from '../globals/KED.env';
import { WeekPlannerPersistedState } from "../components/weekplanner/weekplanner-persisted-state";
import moment from 'moment';
import { createUUID, DocumentAccess } from 'kedbackend/client';
import { getAdjustedWeek, KEDWeek } from '../utils/weekutil';

export let userTasksRepo = env.userTasksRepo as UserTasksRepo;

class UserTasksRepo extends KedRepo<UserTask> {
  persistedState: WeekPlannerPersistedState | null;

  constructor() {
    super({
      getClient() { return env.kedBackendClient; },
      optimistic: true,
      table: "usertasks",
      user: env.currentUser ? env.currentUser.mail : "",
      getQueryOptions: async () => {
        const userEmail = env.currentUser ? env.currentUser.mail : "";
        if (!this.persistedState)
          this.persistedState = await WeekPlannerPersistedState.load(userEmail);
        const { weekDate } = this.persistedState;
        const weekNumber = moment(weekDate).week();
        const kedWeek = KEDWeek(moment(weekDate).year(), weekNumber);
        /*const [from, to] = [moment(weekDate).startOf('week'), moment(weekDate).endOf('week')]
          .map(m => m.toDate().getTime());*/
        
          return {
            from: kedWeek.notBefore,
            to: kedWeek.notAfter,
            role: "USER",
            include: ["task", "course"],
            flags: ["includeIdsOnly"],
          };
        }
    });
    this.persistedState = null;
  }

  async updatePersistedState(stateChanges: Partial<WeekPlannerPersistedState>) {
    Object.assign(this.persistedState, stateChanges);
    await this.persistedState.save();
    this.mem.notifySubscribers();
  }

  async setTaskDoneState(userTask: UserTask, done: boolean) {
    const {getClient, optimistic, table} = this.options;
    const client = getClient();
    const modifiedItem = Object.assign({}, userTask, {done});
    if (optimistic) await this.mem.update(Object.assign({}, modifiedItem, {$meta: 'updating'}));
    // Now make sure to change the state for other weeks as well:
    const similarTasks = await client.list<UserTask>('usertasks', {
      name: userTask.name,
      role: "USER",
      include: ["task","course"],
      flags: ["includeIdsOnly"],
      from: moment(this.persistedState.weekDate).add(-3, 'weeks').valueOf(),
      to: moment(this.persistedState.weekDate).add(3, 'weeks').valueOf()
    }, {
      cache: 'no-cache'
    });
    const identicalTasks = similarTasks.filter(t =>
      t.courseName === userTask.courseName &&
      t.learningGoal === userTask.learningGoal &&
      (!userTask.task || t.task.map(t=>t.id).join('') === userTask.task.map(t=>t.id).join('')) &&
      (!userTask.course || t.course.map(c=>c.id).join('') === userTask.course.map(c=>c.id).join('')));
    await this.update(identicalTasks, t => t.done = done);
  }

  async setSubTaskDoneState (userTask: UserTask, subTaskId: string, done: boolean) {
    const {getClient, optimistic, table} = this.options;
    const client = getClient();
    await this.update([userTask],
      t => t.subTasks && t.subTasks.filter(st => st.id === subTaskId)
        .forEach(st => st.done = done));
  }

  async setWeekPlannerBoxOpen(courseName: string, isOpen: boolean) {
    const openCourses = { ...this.persistedState.openCourses };
    if (isOpen)
      openCourses[courseName] = true;
    else
      delete openCourses[courseName];
    await this.updatePersistedState({openCourses});
  }

  async changeWeek(weekDate: Date) {
    let persistedState = this.persistedState;
    if (!persistedState) {
      persistedState = await WeekPlannerPersistedState.load(env.currentUser.mail);
    }
    const newPersisted = new WeekPlannerPersistedState(persistedState);
    newPersisted.weekDate = moment(weekDate).startOf('week').valueOf();
    newPersisted.save();
    this.persistedState = newPersisted;
    await this.mem.refreshFromServer();
  }

  subscribe(subscriber: (userTasks: UserTask[], persistedState: WeekPlannerPersistedState, weekTextsUT: UserTask) => void) {
    const proxySubscriber = (userTasks: UserTask[]) => {
      subscriber(
        userTasks.filter(ut => !ut.weekTexts),
        this.persistedState,
        userTasks.filter(ut => !!ut.weekTexts)[0] || {
          id: createUUID(),
          dateTime: this.persistedState.weekDate,
          weekTexts: {assessment:'', strategy:''},
          acl: [
            // Default ACL: Let user self have full control over this item:
            new DocumentAccess("email", env.currentUser.mail, "S"),
            // Additional ACL: Let employees on same school have read access to it.
            // This currently only applies to tasks that refer to course tasks (not own tasks!)
            new DocumentAccess("schoolRole", `${env.currentUser.school}/EMPLOYEE`, "R")
          ].map(ac => ac.toString())
        });
    };
    proxySubscriber["subscriber"] = subscriber;
    this.mem.subscribe(proxySubscriber);
  }

  unsubscribe(subscriber: (userTasks: UserTask[], persistedState: WeekPlannerPersistedState, weekTextsUT: UserTask) => void) {
    this.mem.subscribers = this.mem.subscribers.filter (s => s["subscriber"] !== subscriber);
  }
  
}

if (!userTasksRepo) {
  userTasksRepo = env.userTasksRepo = new UserTasksRepo();
  userTasksRepo.mem.ensureHasData();
}
