import '../../global-setters/set-bearer-providers';
const bearerPromise = env.bearerProvider.getBearer(); // Start getting bearer as soon as possible.
import '../../global-setters/set-ked-backend-client';
import '../../global-setters/set-eds-client';
import * as React from 'react';
import * as ReactDOM from 'react-dom';
import env from '../../globals/KED.env';
import { CourseViewer } from '../../components/course-viewer';
import $ from 'jquery';
import { showError, showInfo } from "../../utils/utils";
import { Spinner } from "../../components/course-builder/sub-components/spinner"; // TODO: Move outside of course-builder!

interface Props {
  courseBuilderUrl: string;
}

interface State {
  isTokenLoaded: boolean;
}

export class KEDAppCourseViewer extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      isTokenLoaded: false
    }
  }

  componentWillMount() {
    bearerPromise.then(()=>{
      this.setState({isTokenLoaded: true});
    }).catch(err => {
      showError(err);
    });
  }

  render() {
    return this.state.isTokenLoaded ?
      <CourseViewer courseBuilderUrl={this.props.courseBuilderUrl} /> :
      <p><Spinner/>V.g. vnta... autenticerar...</p>
  }
}
