import * as React from 'react';
import { LearningModuleTask, TaskList } from './task-list';

interface Props {
  commonTasks: LearningModuleTask[]
  learningGoals: LearningGoal[]
}

export interface LearningGoal {
  name: string;
  learningTasks: LearningModuleTask[];
}

export class LearningGoalsList extends React.Component<Props> {
  render() {
    const {commonTasks, learningGoals} = this.props;
    return <div>{learningGoals.map(lg => <div key={lg.name}>
      <h5>{lg.name}</h5>
      <TaskList learningTasks={lg.learningTasks} />
    </div>)}
    {commonTasks.length > 0 && <div>
      {learningGoals.length > 0 && <h5>vergripande</h5>}
      <TaskList learningTasks={commonTasks} />
    </div>}
    </div>;
  }
}