import * as React from 'react';
import { UserTask, UserSubTask } from 'kedbackend/models';
import { RenameableText } from '../utility-components/renameable-text';
import { L } from '../../utils/utils';
import { getTaskType } from './get-task-type';
import ReactDatePicker, { ReactDatePickerProps } from 'react-datepicker';
import '../../globals/moment-sv-locale';
import {TextInput} from '../utility-components/form-field-text-input';
import moment from 'moment';
import { FormField } from '../utility-components/form-field';
import { AddOrEditSubTask } from './add-or-edit-sub-task';
import { createUUID } from 'kedbackend/client';
import update from 'react-addons-update';
import { userTasksRepo } from '../../repos/user-tasks-repo';
import { TextAreaFormField } from '../utility-components/form-field-textarea';

interface Props {
  userTask: UserTask;
  onUpdate: (updater: (userTask: UserTask) => void) => void;
  onDelete: (userTaskId: string) => void;
  openDialog: (dialog) => void;
  closeDialog: ()=>void;
}

interface State {
  userTask: UserTask;
  name: string;
  description?: string;
  deadline?: string;
  url: string;
  subTasks?: UserSubTask[];
  showCalendar?: boolean;
}

export class EditUserTask extends React.Component<Props,State> {
  datePicker: any;

  constructor(props: Props) {
    super(props);
    this.state = {
      userTask: props.userTask,
      name: props.userTask.name,
      description: props.userTask.description,
      url: props.userTask.url,
      deadline: props.userTask.deadline,
      subTasks: props.userTask.subTasks
    }
    this.onUserTasksChanged = this.onUserTasksChanged.bind(this);
  }

  componentDidMount() {
    userTasksRepo.subscribe(this.onUserTasksChanged);
  }

  onUserTasksChanged(userTasks: UserTask[]) {
    const myUserTask = userTasks.find(ut => ut.id === this.props.userTask.id);
    if (!myUserTask) {
      this.props.closeDialog();
      return;
    }
    this.setState({
      userTask: myUserTask,
      subTasks: myUserTask.subTasks
    });
  }

  componentWillUnmount() {
    userTasksRepo.unsubscribe(this.onUserTasksChanged);
  }

  isModified() {
    // Don't count subtask changes! They live their own life and is maintained
    // in add-or-edit-sub-task.tsx. Reason: User would expect added /edited subtasks
    // to be persisted right away. May click away this dialog.
    // Also reason: Can invoke that dialog by itself from other components. From WeekPlanner
    // when clicking the subtask for example!

    const {deadline, description, name, url, userTask} = this.state;
    return (
      deadline !== userTask.deadline ||
      description !== userTask.description ||
      name !== userTask.name ||
      url !== userTask.url);
  }

  addSubTask(): any {
    this.props.openDialog(<AddOrEditSubTask
      mode="add"
      userTask={this.state.userTask}
      closeDialog={this.props.closeDialog}
    />);
  }

  editSubTask(subTask: UserSubTask) {
    this.props.openDialog(<AddOrEditSubTask
      mode="edit"
      subTask={subTask}
      userTask={this.state.userTask}
      closeDialog={this.props.closeDialog}
    />);
  }
  
  render() {
    const { name, description, url, deadline, subTasks, showCalendar, userTask } = this.state;
    const { id, courseName } = userTask;
    const { onUpdate, onDelete } = this.props;
    const isModified = this.isModified();

    const taskType = getTaskType(userTask);
    const isCustomTask = taskType === 'customTask';
    const expired = moment(userTask.deadline) < moment();

    return <div className="editTaskDialog sv-html-portlet sv-portlet sv-skip-spacer">
      <h2>{isCustomTask ? `Redigera arbetsml` : `Redigera uppgift`}</h2>
      <hr />
      <TextInput
        autoFocus
        label={isCustomTask ? "Arbetsmlets namn" : "Uppgiftens namn"}
        id="EditUserTask:name"
        placeholder={isCustomTask ? "Vad ska du gra?" : "Ange uppgiftens namn..."}
        value={name}
        onChange={name => this.setState({name})} />
      
      {isCustomTask && <>
        <TextAreaFormField
          rows={5}
          label="Beskrivning"
          id="EditUserTask:description"
          placeholder="Lgg till en beskrivning..."
          value={description}
          onChange={description=>this.setState({description})} />

        <TextInput
          label="Lnk"
          id="EdutUserTask:url"
          placeholder="http(s)://..."
          value={url}
          onChange={url => this.setState({url})} >

        </TextInput></>}

        <FormField label="Ange deadline">
          {(deadline || showCalendar) ?
            <div className="align-horizontal">
              <div className="horizontalItem top">
                <ReactDatePicker ref={elem => this.datePicker = elem}
                  id="EditUserTask:deadline"
                  selected={deadline && moment(deadline)}
                  autoFocus={showCalendar}
                  dateFormat="YYYY-MM-DD"
                  className={expired ? "expired" : undefined}
                  locale={"sv"}
                  popperPlacement={isCustomTask ? "top-start" : "bottom-start"}
                  onBlur={()=>this.setState({showCalendar: false})}
                  onChange={value=>{
                    this.setState({
                      deadline: value && value.format("YYYY-MM-DD"),
                      showCalendar: false
                    });
                  }}
                />
              </div>
              <div className="horizontalItem top">
                <a className="deleteDate" href="#" title="Ta bort deadline" onClick={ev=>{
                  ev.preventDefault();
                  this.setState({deadline: null, showCalendar: false});
                }}></a>
              </div>
            </div> :
            <div className="top" ref={()=>{this.datePicker = null}}>
                <a className="btn" onClick={ev => {
                  if (this.datePicker) {
                    this.datePicker.setOpen(true);
                  }
                  this.setState({
                    showCalendar: true
                  });
                }}>
                <i className="fa fa-calendar" aria-hidden="true"></i> Ange deadline...
              </a>              
            </div>}
        </FormField>
      <FormField label="Underuppgifter">
        <div className="learningGoalTasks">
          <div className="taskContainer">

              {subTasks && subTasks.map(subTask => <div key={subTask.id} className="align-horizontal">
                <div className="horizontalItem top"><a
                  onClick={()=>this.editSubTask(subTask)}
                  href="#">{subTask.name}</a></div>
              </div>)}
          </div>
        </div>
        <div className="align-horizontal">
          <div className="top">
            <button id="EditUserTask:addSubTask" className="btn"
              onClick={()=>this.addSubTask()}>
              <i className="fa fa-plus" aria-hidden="true"></i> Lgg till underuppgift
            </button>
          </div>
        </div>
      </FormField>
      <div className="divider large" />
      <div className="align-horizontal">
        <div className="horizontalButton top">
          <button tabIndex={1}
            className="btn btn-default"
            onClick={()=>{
              isModified ? onUpdate(userTask => {
                userTask.name = name;
                userTask.description = description;
                userTask.url = url;
                userTask.deadline = deadline;
              }) : this.props.closeDialog();
            }}> Spara </button>
        </div>
        <div className="horizontalButton top">
          <button tabIndex={1}
            className="btn"
            onClick={()=>{
              this.props.closeDialog();
            }}> Avbryt </button>
        </div>
        <div className="confirm top pull-right">
          <button
            tabIndex={1}
            className="btn btn-warning"
            onClick={()=>{
              onDelete(id);
            }}>{isCustomTask ?
              " Ta bort arbetsml" :
              " Ta bort den hr uppgiften"
          }</button>
        </div>
      </div>
                  
      {/*<div className="btn btn-warning btn-large pull-right" onClick={ev => onDelete(id)}>
        {L`Ta bort arbetsml`}</div>
      {isModified &&
        <div
          className="btn btn-large btn-default"
          tabIndex={0}
          onClick={()=>onUpdate(userTask => {
            userTask.name = name;
            userTask.description = description;
            userTask.url = url;
          })}> Spara</div>}*/}
    </div>
  }
}

