import * as React from 'react';
import { CentralContent } from 'kedbackend/models';

interface Props {
  title: string;
  subTitle?: string;
  centralContent: CentralContent[];
  className?: string;
  headerOpen?: boolean;
}

interface State {
  headerOpen: boolean;
}

export class CentralContentBox extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      headerOpen: props.headerOpen || false
    };
  }

  render() {
    const { title, subTitle, centralContent, className } = this.props;
    const { headerOpen } = this.state;

    return <div className={(className || '') + " openClose" + (headerOpen ? " open" : "")}>
      <div className="openHeader" onClick={() => this.setState({ headerOpen: !this.state.headerOpen })}>
        <p>{title}</p>
      </div>
      <div className="openContent">
        <div className="abilityList">
          {subTitle && <div className="contentHeader">
            <h5>{subTitle}</h5>
          </div>}
          {centralContent.map(cc =>
            <div key={cc.id} className="contentPart">
              <div className="contentText">
                <p dangerouslySetInnerHTML={{__html: cc.name}} />
              </div>
            </div>
          )}
        </div>
      </div>
    </div>;
  }
}
