import * as React from 'react';
import { RemoveItem } from '../course-builder/sub-components/remove-item';
import { findDOMNode } from 'react-dom';
import $ from 'jquery';

interface Props {
  dialogs: React.Component[];
  popDialog: ()=>void;
}

export class Dialogs extends React.Component<Props> {
  lastDiv: HTMLDivElement | null;

  constructor(props: Props) {
    super(props);
  }

  componentDidUpdate(prevProps: Props) {
    if (this.lastDiv !== null && prevProps.dialogs.length > this.props.dialogs.length) {
      // A dialog was closed. Now focus the last dialog:
      $(findDOMNode(this.lastDiv)).find(':input').first().focus();
    }
  }

  render () {
    this.lastDiv = null;
    const {dialogs, popDialog} = this.props;
    $('body').attr('aria-disabled', dialogs.length > 0);
    $('body').css('overflow', dialogs.length > 0 ? 'hidden' : 'auto');

    return dialogs.length > 0 && <div>
      {dialogs.map((dialog, idx) => {
        let div: HTMLDivElement;
        function onKeyPress(ev: React.KeyboardEvent<HTMLDivElement>) {
          if (ev.which === 13 && (!ev.target || (ev.target as HTMLElement).tagName !== 'TEXTAREA')) {
            $(findDOMNode(div)).find('.btn-default').click();
          }
          ev.stopPropagation();
        }
        function onKeyDown(ev) {
          if (ev.which === 27) { // Escape
            popDialog();
            ev.stopPropagation();
            return;
          }
          if (ev.which === 83 && (ev.ctrlKey || ev.metaKey)) { // CTRL-S
            const domNode = findDOMNode(div);
            ev.preventDefault();
            const defaultButton = $(domNode).find('.btn-default');
            defaultButton.click();
            ev.stopPropagation();
          }
        }
        return <div key={idx}>
      <div className="darken" />
      <div className="modal-page-wrap">
        <div className="modal-page" ref={elem => {
          div = elem;
          if (idx === dialogs.length - 1) this.lastDiv = elem;
        }}
            tabIndex={0}
            aria-disabled={idx < dialogs.length - 1}
            onKeyPress={onKeyPress}
            onKeyDown={onKeyDown}>
          {dialog}
          <RemoveItem onClick={popDialog} />
          <div className="stopFloats"></div>
        </div>
      </div>
    </div>})}
  </div>;
  }
}
