import * as React from 'react';

interface Props {
  visible: boolean;
  text: string;
  className?: string;
  onConfirm: ()=>void;
  onCancel: ()=>void;
}

export function Confirmation (props: Props) {
  return <div
    className={`confirmation-box ${props.className}${props.visible ? " visible" : ""}`}>
    <p>{props.text}</p>
    <button onClick={()=>props.onConfirm()}>OK</button>
    <button onClick={()=>props.onCancel()}>Avbryt</button>
  </div>;
}
