import * as React from 'react';
import { L } from '../../../utils/utils';
import { Course, CourseModule } from 'kedbackend/models';
import env from '../../../globals/KED.env';
// Views
import { AbilitiesBox } from '../subcomponents/abilities-box';
import { CentralContentBox } from '../subcomponents/central-content-box';
import { KnowledgeRequirementsTable } from '../subcomponents/knowledge-requirements-table';
import { ResourceList } from '../subcomponents/resource-list';

interface Props {
  course: Course;
}

interface State {
}

export class RunUpPage extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.state = {}
  }

  render() {
    const { course } = this.props;
    return <div>
      <div className="sv-html-portlet sv-portlet sv-skip-spacer">
          {course.runUpText && <div>
            <h1>Upptakt</h1>
            <p className="normal" dangerouslySetInnerHTML={{__html: course.runUpText}} />
          </div>}
          {course.responsibleTeachers && course.responsibleTeachers.length > 0 && <div>
            <h2>Lrare</h2>
            {course.responsibleTeachers.map((teacher, i) =>
              <p key={i}><a href={interpretTeacherUrl(teacher.url)}>{teacher.name}</a></p>
            )}
          </div>}
          {course.workFlowLink && <a className="btn" href={course.workFlowLink}>Arbetsgng</a>}
          <br />
          <br />
          <AbilitiesBox
            title="mnets frmgor"
            subTitle="Frmgor"
            abilities={course.abilities}
            className="larger" />

          <CentralContentBox
            title="Kursens centrala innehll"
            subTitle="Centralt innehll"
            centralContent={course.centralContent}
            className="larger" />

          <KnowledgeRequirementsTable
            title="Kursens kunskapskrav"
            requirements={course.knowledgeRequirements}
            className="larger" />

        </div>
        {course.resources.length > 0 && <div className="sv-html-portlet sv-portlet">
          <div className="ked_boxed">
            <h2>Resurser</h2>
            <hr />
            <ResourceList resources={course.resources} />
          </div>
        </div>}
      </div>
  }
}

function interpretTeacherUrl(url: string) {
  if (url.indexOf(':') === -1 && url.indexOf('@') >= 0) return "mailto:" + url;
  return url;
}
