import * as React from 'react';
import { FormField } from './form-field';

interface Props {
  label: any;
  id: string;
  value: string;
  placeholder?: string;
  autoFocus?: boolean;
  children?;
  onChange: (value: string) => void;
}

export class TextInput extends React.Component<Props> {
  constructor (props: Props) {
    super(props);
  }

  render() {
    return (
      <FormField label={this.props.label}>
        <div className="align-horizontal">
          <div className="horizontalItem top">
            <input type="text" autoFocus={this.props.autoFocus} id={this.props.id} size={35} value={this.props.value}
              onChange={ev => this.props.onChange(ev.target.value)}
              placeholder={this.props.placeholder} />
          </div>
          {!!this.props.children && <div className="horizontalItem">
            {this.props.children}
          </div>}
        </div>
      </FormField>
    );
  }
}

